@import '../../../../theme/variables.scss';
@import '../../../../theme/responsive-styles.scss';

.carrier-profile-desktop {
  --background: #F8F8F8 !important;
}

.carrier-subheading {
  font-size: 12px;
  color: #555555;
}

.carrier-card-title {
  font-size: 16px;
  color: #000;
  font-family: $font-gtamerica-bold;
  font-weight: bold;
}

.profile-form-control {
  font-size: 14px;
  color:var(--ion-color-light-contrast);
  font-family: $font-gtamerica-regular;
  font-weight: bold;
  
   input  {
    --padding-top: 0 !important;
    --padding-bottom: 0 !important;
  }
}

.desktop-manage-tabs{
  height:371px;
  width:1148px;
  margin-left:131px;
  margin-right:121px;
  margin-top: 30px;
  .page-header-searchResults{
    height: 0px;
} 
}
.certificate-png {
  width: 35.5px !important;
  height: 35.5px;
}
.certificate-card {
  height: 81px;
  padding: 18px 10px 15.5px 13px;
  box-shadow: 0px 3px 6px #3a4c8212;
  border: 0.5px solid #dedede;
}
.carrier-profile-label {
  padding-top: 5px;

}
.Hazmat {
  color: var(--ion-color-light-contrast);
  background: var(--ion-item-background, transparent);
  font-size: 14px;
  font-family: $font-gtamerica-regular;
}
.HazmatDate {
  text-align: right;
  background: var(--ion-item-background, transparent);
}

.certification-header {
  font-size: 14px;
  color: var(--ion-color-primary);
  padding-left: 23px;
  padding-top: 7px;
  font-family: $font-gtamerica-regular;
}
.carrier-profile-ionitem {
  width: 100%;
  padding-right: 15px;
  margin-bottom: 10px;
  ion-label {
    padding-left: 15px;
    font-size: 13px;
    margin-top: 0;
  }
  ion-input {
    --padding-start: 15px !important;
    font-size: 15px;
  }
}
.carrier-profile-list {
  padding: 24px 0 10px;
}
.carrier-ionitem2 {
  padding-top: 5.52px;
}
.profile-form-control2 {
  margin-bottom: 13px;
}
.profile-form-control2 .input {
  padding-left: 6%;
}

.Insurance-png {
  width: 28.78px !important;
  height: 35.16px;
}
.InsuranceCol {
  text-align: left;
  padding-left: 0;
  margin-top: 8px;
}
.insurance-carrier-card {
  margin-top: 11.5px;
  box-shadow: 0px 3px 6px #3a4c8212;
  border: 0.5px solid #dedede;
}
.contact {
  margin: 5% 2% 0% 4%;
  font-size: 13px;
  line-height: 18px;
  color: #000;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    margin: 1% 1% 0% 1%;
  }
  .email {
    color: #FF6418;
  }
  span {
    display: block;
  }
}
.InsuranceRow {
  width: 100%;
  padding-bottom: 8px;
}
.insurence-title {
  padding: 9px 0;
  display: block;
  color: #000;
}
.Approved {
  margin-top: 7px !important;
}
.date_approved{
  margin-top: 0px !important ;
}
.accordianCardContent{
  padding: 0;
}
.carrier-accordion-header{
  font-size: 14px;
  color: var(--ion-color-primary);
  font-family: "Roboto Regular";
}
.carrier-card{
  margin:15px 6px 7.5px 6px!important;
  border-radius: 4px;
  box-shadow: 0px 3px 6px #3a4c8212;
  border: 0.5px solid #dedede;
  background-color: var(--ion-color-primary-contrast);
}
.carrier-card2{
  margin: 26.5px 4px 7px 4px !important;
  border-radius: 4px;
  box-shadow: 0px 3px 6px #3a4c8212;
  border: 0.5px solid #dedede;
  background-color: var(--ion-color-primary-contrast);
}
.acccoridan-plus{
  width: 18% !important;
}
.card-header-pad{
  padding:  15.5px 10.5px 15.5px 11.37px;
}
.card-header-pad2{
padding:  15.5px 10.5px 0px  11.37px;
}
.insurance-item{
  padding-top: 6px;
  padding-bottom: 14px;
  padding-left: 12px;
  padding-right: 7px;
  font-size: 13px !important;
    
}
.addAccordian{
  float: right;
}
.insurance-card-header {
  padding: 26.5px 10px 0;
}
.insurance-card-list {
  padding: 10px 0px;
}

.inner-card-title {
  padding: 10px;
  font-weight: bold;
  font-size: 16px;
  color: #000;
  display: block;
}
.cert-header {
  padding: 0 10px;
}

.insurence-icon {
  font-size: 36px;
}

.tick-icon {
  font-size: 14px;
  position: relative;
  left: 20px;
  top: 10px;
}

.cert-desc {
  padding: 7px 0;
  display: block;
  color: #000;
  font-size: 15px;
}

.qualficiation-desc {
  padding: 0;
  display: block;
  color: #000;
  font-size: 15px;
}

.qualficiation-expiry {
  padding: 0;
  display: block;
  color: #9C9089;
  font-size: 15px;
  text-align: right;
    padding: 5px 10px 0;
}

.cert-grid {
  margin-bottom: 20px;
}

.carrier-profile-desktop {
  .carrier-profile-ionitem {
    width: 50%;
    display: inline-block;
  }
  .carrier-text {
    font-size: 14px;
    padding: 10px 13px;
    margin-top: 20px;
    display: block;
    &.insurence {
      padding: 0 25px;
      padding-bottom: 20px;
      margin-top: 0;
    }
  }
  .insurance-card-header {
    border-bottom: 1px solid #cecece;
    margin: 0 20px;
    margin-bottom: 20px;
  }

  .insurence-title {
    font-size: 15px;
    font-weight: bold;
    position: relative;
    right: 60px;
  }
  .cert-grid {
    background: #fff;
    padding: 20px;
    height: 80px;
    border-radius: 4px;
  }
  .carrier-card-title {
    padding: 15px;
    display: block;
  }
  .cert-desc {
    text-transform: uppercase;
    font-weight: bold;
  
  }
  .qualficiation-desc {
    font-weight: bold;
  }
  .carrier-accordion-header {
    position: relative;
  }
  .addAccordian {
    position: absolute;
    right: 30px;
    top: 30px;
  }
  .contact {
    span {
      display: inline;
    }
  }
}
