/*Modified Styles*/
@import '../../../theme/variables.scss';
@import '../../../theme/responsive-styles.scss';

ion-toolbar {
  ion-title {
    color: $white;
  }
}
ion-header {
  background-color: $wildSand;
  height: 150px;
  text-transform: capitalize;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    height: 200px;
    padding-top: 70px;
  }

  .header_toggle_div {
    width: 239px;
    margin: auto;
    padding-bottom: 10px;
    margin-top: 14px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      margin-top: 42px;
    }
  }
  transition: top 0.6s;
}
.page-header .header-desc {
  height: 100%;
}
.page-header ion-col {
  height: 100%;
  display: table;
}
.page-header ion-col * {
  vertical-align: middle;
  display: table-cell ;
}
.hide-header {
  top: -50px;
}
.dropDown_col {
  margin-left: -41px;
  padding-right: -13px;
  padding-left: 17px !important;
}
.sort_label {
  margin-left: 10px;
  margin-top: 5px;
  color: #666666;
  font-size: 12px;
  font-family: $font-gtamerica-regular;
}
.sort_label_search {
  font-weight: bold;
}
.short-div {
  padding: 0px 0 10px 0;
  background-color: #E9E5E1;
  @media only screen  and (min-width :map-get($grid-breakpoints, lg)) {
    background-color: #f8f8f8;
    padding: 20px 20px 10px 20px;
    margin-top: 18px;
    display:none;
   }
  ion-col {
    padding: 0;
    margin-top: -21px;
  }
  .short-row {
    padding-top: 20px;
    margin-top: 15px;
    margin-bottom: 22px;
    @media only screen  and (min-width :map-get($grid-breakpoints, lg)) {
      padding-top: 0;
    }
  }
  .rec_text {
    font-size: 13px;
    color: #645A53 !important;
    padding-top: 14px;
    padding-bottom: 0;
    font-family: $font-gtamerica-regular;
    .recommendations_num {
      font-size: 13px;
      color: #645A53;
    }
  }
  ion-badge {
    ion-label {
      font-size: 11px;
    }
    .badge_sortby_select {
      color: var(--ion-color-primary);
      font-size: 11px;
      margin-top: -7px;
      float: left;
      padding-left: 8px;

      .select-placeholder {
        opacity: 0.8;
      }
    }
  }
  .badge_sortby_select .select-icon-inner {
    margin-top: -7px;
  }
  .header_badge_select {
    margin-left: 12px;
    width: 85px;
    ion-select {
      top: -8px;
      float: left;
      padding: 9px;
    }
  }
}
.card_filter_badge {
    height: 15px;
    background-color: transparent;
    color: #151724;
    font-size: 12px;
    font-family: "Roboto Regular";
    border-radius: 0;
    font-weight: normal;
    padding: 0 13px 0 13px;
    position: relative;
    margin-top: 6px;

}
.card_filter_badge:after {
  content: '';
  height: 75%; //You can change this if you want smaller/bigger borders
  width: 1px;
  position: absolute;
  right: 0;
  top: 2px; // If you want to set a smaller height and center it, change this value
  background-color: #999999; 
}
.card_filter_badge:last-child:after {
  height: 0%;
  width: 0%;
}
#header-title {
  font-size: 4.81vw;
  color: #F85F14;
  text-align: center;
  font-family: $font-gtamerica-bold;
  padding-left:25px;
}
#header-title-sm{
  text-transform: none;
  font-size: 4.81vw;
  color: #F85F14;
  text-align: center;
  font-family:$font-gtamerica-bold;
    padding: 34.5px 0 12px 5.6vw;
  margin:auto;
  width:1%;
}
.card_arrow_img {
  width: 24px !important;
  content: url("/assets/icon/arrow.svg");
  @media only screen  and (min-width : map-get($grid-breakpoints, lg) ) {
    width: 54px !important;
    content: url("/assets/icon/arrow-desktop.svg");
    margin-top: 19px;
  }
}
.card-content {
  padding-left: 0px;
  padding-right: 0px;
    width: 100%;
    height: auto;
    background: linear-gradient(97deg, #F3F1EF 31%, #F3F1EF 32%, #F3F1EF 32%, #FFFFFF 21%, #FFFFFF 10%);
    .load-grid{
    padding: 20px 10px;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    background-color: #ffffff;
  }
}

.card-col {
  size: 5.5px;
}

.card-name {
  font-size: 14px;
  color: #ff6418;
  opacity: 0.8;
  font-family: $font-gtamerica-medium;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    opacity: 1;
  }
}

.card-dt {
  font-size: 12px;
  color: #000000;
  font-family: $font-gtamerica-regular;
}

.card-dist {
  font-size: 12px;
  color: #666;
  font-family: $font-gtamerica-regular;
}
.card-load{
  padding-bottom: 10px;
  font-size: 12px;
  color: #666;
  font-family: $font-gtamerica-regular;
}
.card-icon {
  font-size: 30px;
  margin-top: 13px;
}

.right {
  text-align: right;
  padding-right: 0;
}

.card_price {
  text-align: right;
  font-size: 18px;
  color: #333333;
  font-family: $font-gtamerica-medium;
}
.sn-toggle {
  .sn-layer {
    position: relative;
    top: 50%;
    width: 100%;
    height: 38px;
    overflow: hidden;
    background-color: $grey;
    z-index: 1;
    border-radius: 100px;
  }
  button {
    background: none;
    margin-left: 4px;
    font-size: 14px;
    line-height: 2;
    border: none;
    min-width: 64px;
    margin-top: 4px;
    padding-left: 18px;
    text-align: left;
  }

  button:last-child {
    margin-right: 5px;
    float: right;
    padding-right: 12px;
  }

  button:active,
  button:focus {
    border-radius: 20px;
    background-color: $white;
    outline: none;
  }
}
.new-search {
  width: 325px;
}
.recommended {
  width: 239px;
}
ion-item-divider {
  min-height: 1px !important;
}
.recommended-Error {
    font-size: 15px;
    padding: 0px;
    margin: 35px 47px 15px 47px;
    color: #666666;
    font-family: $font-gtamerica-regular;
}
@media (min-width: map-get($grid-breakpoints, lg)) {
  .recommended-Error {
    margin: 35px 25px 15px 25px;
  }
}
.recommended-Error-img {
  opacity: 1;
}
.No-Recommended {
  text-align: center;
  padding-top: 10.09vh;
  @media (max-height: map-get($grid-height-breakpoints, xs)) {
    padding: 0;
    padding-top: 0;
  }
  @media (min-width: map-get($grid-breakpoints, lg)) {
    width: 26vw;
    margin: auto;
  }
}
.RecommendedSearch {
    text-transform: inherit;
    font-size: 16px;
    height: 42px;
    --background: #FFFFFF;
    --border-radius: 0;
    --box-shadow: none;
    border: 1.25px solid #FF6418;
    color: #000000;
    --background-activated: #FFFFFF;
    --background-focused: #FFFFFF;
    --background-hover: #FFFFFF;
    font-family: $font-gtamerica-regular;
    letter-spacing:0px;
    ion-img{
      margin-right: 9px;
      width: 16px;
    height: 16.16px;
    }
}
.SearchLoadBtn {
  padding-top: 65px;
  @media (max-height: map-get($grid-height-breakpoints, xs)) {
    padding-top: 20px;
  }
}
.recommended-error-btn-p{
  font-size: 15px;
  font-family:$font-gtamerica-regular;
  color: #666666;
  padding-top: 16.5px;
    padding-bottom: 16.5px;
}
.load-search {
  font-size: 1.5rem;
}

.loadCardFooter {
  padding-top: 6px;
  ion-col{
    padding-left: 0;
  }
  .card_filter_badge:first-child{
    padding-left: 2px;
  }
}
.loadCardArrow {
  text-align: -webkit-center;
  // padding-top: 19px;
}

.sortby_select_div {
  height: 22px;
  width: 148px;
  text-align: left;
  border-radius: 12px;
  margin-left: 5px;
  ion-label {
    font-size: 11px;
  }
  ion-select {
    margin-left: 3px;
  }
}
.select_div {
  display: -webkit-inline-box;
  height: 21px;
  float: right;
}

.margin_end {
  margin-left: 100px;
}

.last_select_div {
  width: 80px;
  margin-left: 40px;
}
.header_edit_search_button {
  margin-right: 16px;
  float: right;
  margin-top: -35px;
  .button_solid {
    border-radius: 17px;
  }
}
.header_search_bubble {
  width: 100%;
  padding: 15px 15px 0 15px;
}
ion-segment-button {
  --indicator-color-checked: #f6f6f6;
}
.homeDesktop{
  display:none;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    display:block;
  }
}
.loadTilePad {
  padding-top: 0;
  background: #E9E5E1;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    background-color: #f8f8f8;
    --padding-start: 20px !important;
    padding: 0 20px 10px 20px;
    margin-bottom: 50px;
    display:none;
  }
}
.logout_btn {
  width: 100px;
  border-radius: 20px;
}
a {
  text-decoration: none;
}
.load_details_header {
  max-height: 81px !important;
  padding-left: 10px;
  .header_title {
    margin-top: 35px;
    color: #ffffff;
    font-size: 22px;
  }
}
.segment_control_tabs {
  ion-segment-button {
    // --color: #000000;
    width:16%;
     --background-checked: #ffffff;
     --color-checked: #000000;
     border: none;
     font-size: 15px;
     color:#81766E;
     font-family:$font-gtamerica-regular;
     ion-label{
      padding-bottom:9px;
      padding-top: 9px;
     }
  }
  .segment-button-checked {
   border-bottom: 4px solid #F85F14;
   border-radius:1px;
   color: #04081E;
   ion-label{
    padding-bottom:5px;
   }
  }
  .segment_recommended {
    width: 34.5vw;
  }
  @media only screen  and (min-width : map-get($grid-breakpoints, lg) ) {
    width: 21.82vw;
    height: 45px;
    padding-top: 0.25vw;
    margin: 0vw auto;
    border-radius: 0;
    border: none;
    padding: 0;
    margin-left: 0;
    background: #FFFFFF;
    ion-segment-button{
      width: auto;
      height: 99%;
      // max-width: 9vw;
      border-radius: 0;
      --background-hover: #FFFFFF;
      padding: 0px;
      ion-label {
        font-size: 15px;
        color: #81766E;
        
      }
    }
    .segment-button-checked { 
      box-shadow:none;
      border-bottom: 4px solid #F85F14;
      ion-label {
        color: #000000;
       
      }
    }
  }
}
.priceLogo {
  width: 25.78px !important;
  height: 25px !important;
  float: right;
}

