.visitedCard {
  width: 4px;
  height: 114px;
  background-color: #a9d671;
  float: left;
  margin-left: -3px;
}
.sortArrowsCol {
  padding-left: 5px !important;
}
.gps-list {
  color: #666666;
  font-size: 8px;
  font-family: $font-gtamerica-regular;
  height: 90vh;
  overflow: auto;
}
.app-page{
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
   background-color: #ff6418;
  }
}
.tab-bar-profile{
  display: none;
}
.copyright-div{
  display: none;
}
.tab-bar-logo{
  display: none;
}
@media only screen and (min-width: map-get($grid-breakpoints, lg)) {
  .tab-bar-div {
    background: #ffffff;
    height: 100%;
    width:100%;
    margin: 0 auto;
    position: relative;
    .tab-bar-logoDiv{
      padding: 0 9.38vw;
    }
    .tab-bar-logo {
      display: unset;
      margin-top: 33px;
      height: 34.51px;
      width: 116px;
      position: absolute;
      z-index: 10;
    }
    .carrier-content {
      position: relative;
      height: 100%;
      margin-top: 97px
    }
    .tab-bar-profile {
      position: absolute;
      top:0;
      right: 0;
      margin-top: 25px;
     padding:0 9.38vw;
      display: unset;
      ion-img{
        height: 42px;
        width: 42px;
      }
      ion-col{
        padding: 0;
      }
      ion-col:last-child{
        padding-left: 6px;
      }
      .name-col {
        padding-left: 5px;
        span:first-child{
          font-size: 11px;
          color: #999999;
          font-family: $font-gtamerica-regular;
        }
        span:last-child{
          font-size: 14px;
          color: #282828;
          font-family: $font-gtamerica-medium;
        }
      }
      .downArrow{
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        border: 1.5px solid #FF6418;
        border-width: 0 1.5px 1.5px 0;
        display: inline-block;
        padding: 3.5px;
        margin-top: 10px;
        margin-right: 13px;
      }
    }
  }
  .desktop-page-container{
    padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
    margin: auto;
    border: 1px solid #f3f3f3;
    background: #E9E5E1;
    .desktop-page{
      padding: 0px 9.38vw 190px 9.38vw;
    }
  }
  .copyright-div{
    display: block;
    background-color: #5e5e5e;
    height: 2.9vh;
    color: #ffffff;
    font-size: 1.25vmin;
    padding: 0.5vh 7vw;
    bottom:0;
    span:last-child{
      float: right;
    }
  }
}
.sort_select{
  margin-left:-4px;
}