/*------ WATCH TOGGLE CSS ---------*/
.switch {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 28px;
  margin-top: 4px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #c0c0c0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: '';
  height: 23px;
  width: 23px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #ff7c3b;
}

input:focus + .slider {
  box-shadow: 0 0 1px #ff7c3b;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(72px);
}

.slider:after {
  content: 'WATCH';
  color: white;
  display: block;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 55%;
  font-size: 11px;
  font-family: $font-gtamerica-regular;
}

input:checked + .slider:after {
  content: 'WATCH';
  margin-left: -10px;
}

