//Primary_Theme

$primary: #ff6418;
$secondary: #3e3e3e;
$danger: #ff0000;
$light: #fdebdf;
$dark: #3e3e3e;
$white: #fff;
$lightBlack: #00000066;
$black: #000000b2;
$silver: #bebebe;
$nobel: #b4b4b4;
$wildSand: #f6f6f6;
$karry: #fee7d8;
$doveGray: #696565;
$darkPink: #f6d6791a;
$brown: #ff64184d;
$tundora: #4a4a4a;
$darkgray: darkgray;
$alto: #dedede;
$darkblue: #3a4c8212;
$grey: #dddedfa1;

$colorTop: #ff8447;
$colorBottom: #ff6e25;
$gradientTop: #fa934f;
$gradientBottom: #f5560f;
$loginBtnTop: #fe8347;
$loginBtnBottom: #fe6d25;

