/* Set variables for all modes */
:root {
  /* Set the background of the entire app */

  /* Set the font family of the entire app */
  --ion-font-family: "GT America Regular";
}

/* Set text color of the entire app for iOS only */
.ios {
  --ion-text-color: $secondary;
}

/* Set text color of the entire app for Material Design only */
.md {
  --ion-text-color: $secondary;
}
// Font path is used to include ionicons,
// Helvetica Neue and Roboto fonts

@import "typography.scss";

@import "responsive-styles.scss";

// @import "Helvetica";
$font-gtamerica-regular: "GT America Regular";
$font-gtamerica-bold: "GT America Bold";
$font-gtamerica-medium: "GT America Medium";

.tab_footer_label {
  font-size: 12px;
  margin-top: 1px;
  font-family: $font-gtamerica-regular;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    //margin-top: -17px;
    margin-left: 12px;
    font-size: 14px;
    display: block;
    margin-top: -17px;
  }
}
.search_label {
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    //margin-top: -19px;
    margin-left: 23px !important;
  }
}
.execute_label {
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    margin-left: 35px !important;
  }
}
.tab_footer_label_manage {
  font-size: 12px;
  margin-top: 0;
  font-family: $font-gtamerica-regular;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    font-size: 14px;
    margin-left: 26px !important;
    margin-top: -17px;
  }
}
.tab_footer_label_more {
  font-size: 12px;
  margin-top: 5px;
  font-family: $font-gtamerica-regular;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    font-size: 14px;
    margin-left: 16px;
    margin-top: -13px;
  }
}
