.tab-selected {
  .home_icon {
    content: url("/assets/icon/nav-home-select.svg");
  }
  .search_icon {
    content: url("/assets/icon/nav-search-select.svg");
  }
  .my_load_icon {
    content: url("/assets/icon/nav-myloads-select.svg");
  }
  .manage_icon {
    content: url("/assets/icon/manage_select.svg");
  }
  .more_icon {
    content: url("/assets/icon/more_select.svg");
  }
  color: #000000;
}
ion-tab-bar {
  height: 61px;
  //--background: linear-gradient(180deg, #ffffff, #f3f3f3);
  --background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px -2px 4px #0000000f;
  opacity: 1;
  ion-tab-button {
    color: #645a53;
    // padding-top: 11px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      padding-top: 0px;
    }
  }
  ion-icon {
    height: 18px;
    margin-bottom: 0;
    width: 21px;
    margin-top: -3px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      margin-top: -17px;
      margin-left: -55px;
    }
  }
  .manage_icon {
    height: 18px;
    width: 18px;
    margin-top: -4px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      margin-top: -16px;
    }
  }
  .my_load_icon {
    width: 17.62px;
    height: 17.62px;
    margin-top: -3px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      margin-top: -15px;
    }
  }
  .search_icon {
    height: 19.38px;
    width: 18.72px;
    margin-top: -3px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      margin-top: -17px;
    }
  }
  .more_icon {
    height: 5.18px;
    // margin-top: -8px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      height: 6px;
      width: 23px;
      margin-top: -10px;
    }
  }
}
ion-card-updated {
  margin: 0px 1px 15px 1px !important;
  border-radius: 0px;
  width: 100%;
  height: auto;
  box-shadow: 0px 3px 6px #645a531a;
  opacity: 1;
}
ion-card {
  margin: 0px 1px 10px 1px !important;
  border: 0.5px solid $alto;
  box-shadow: 0px 3px 6px $darkblue;
  border-radius: 0px;
  ion-card {
    margin: 0px;
    box-shadow: none;
  }
}
// ion-content {
//   --background: #fcfcfc;
// }
.custom-padding {
  --padding-top: 1px !important;
  --padding-start: 10px !important;
  --padding-end: 10px !important;
  --background: #e9e5e1;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    --padding-top: 0 !important;
    --padding-start: 20px !important;
    --padding-end: 20px !important;
  }
}
.home-page {
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    --padding-start: 0px !important;
    --padding-end: 0px !important;
    --background: #ffffff !important;
  }
}
.arrow_forward {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  border: 1px solid #ff8a51;
  padding: 4px;
  border-width: 1.5px 1.5px 0 0;
}
ion-back-button {
  --color: white !important;
  --icon-font-size: 30px !important;
  --icon-padding-start: 10px !important;
}
