ion-select {
  .select-icon {
    .select-icon-inner {
      opacity: 0 !important;
    }
  }
}
.select-icon-inner {
  border-top: transparent !important;
}

.select-alert {
  .alert-wrapper {
    height: 259px !important;
    min-width: 205px !important;
  }
  .alert-head {
    padding: 0 !important;
  }
  .alert-title {
    font-family: $font-gtamerica-regular;
    font-size: 14px !important;
  }
  .alert-button {
    font-size: 14px !important;
    font-family: $font-gtamerica-regular;
  }
  .alert-button-group {
    button:first-child {
      color: black;
    }
  }
  .alert-radio-group {
    font-size: 13px;
    button {
      border-bottom: 0.25px solid #0000003f !important;
    }
  }
}

// PDF Viewer
.mobile__pdf__container footer {
  background-image: linear-gradient(180deg, #fca364, #fd7d4f) !important;
}

@media only screen and (min-width: map-get($grid-breakpoints, lg)) {
  .desktop-header-desc {
    ion-grid {
      padding: 0;
      font-size: 12px;
      font-family: $font-gtamerica-regular;
      display: flex;
      height: 28px;
      div {
        display: contents;
      }
      .desktop-title,
      .breadcrumb-devider {
        color: #666666;
        font-size: 12px;
        margin-top: 1px;
      }
    }
    .header-title {
      color: #000000;
      font-size: 22px;
      font-family: $font-gtamerica-medium;
      margin: 10px 0 0 0;
    }
  }
  .end-of-list {
    height: 0px !important;
  }
}

/* styels for data tabel */
.rdt_TableHead {
  font-family: $font-gtamerica-medium;
  font-size: 13px;
}
.rdt_TableHeadRow {
  background: #ededed !important;
  color: #000000;
  font-size: 13px;
}
.rdt_TableCol_Sortable {
  font-size: 13px;
  font-family: $font-gtamerica-medium;
}
.rdt_TableCell {
  font-size: 14px;
  font-family: $font-gtamerica-regular;
}
.rdt_TableRow {
  border: 0.5px solid #cbcbcb !important;
  i {
    border: 1px solid #ff6418;
    border-width: 1px 1px 0 0;
    display: inline-block;
    padding: 3px;
    margin-top: -4px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
}
.rdt_TableHeader {
  font-size: 12px !important;
  color: #666666 !important;
  font-family: $font-gtamerica-regular !important;
  padding: 12px 12px 0 20px !important;
  background-color: #f8f8f8 !important;
  margin-top: -10px;
}
.datatable-badge {
  width: 22px;
  height: 22px;
  box-shadow: none;
  padding: 0px;
  i {
    border: 1px solid #ff6418;
    border-width: 2px 2px 0 0;
    display: inline-block;
    padding: 3px;
    margin-top: -4px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
}
.end-of-list {
  text-align: center;
  height: 60px;
  margin: 20px auto 0px auto;
  position: relative;
  color: #645a53;
  font-weight: $font-gtamerica-regular;
  font-size: 14px;
}
.searchResults_div {
  font-family: $font-gtamerica-medium;
  font-size: 18px;
  width: 132px;
  height: 35px;
  color: #282828;
  margin-top: 25px;
}
.price_text {
  font-size: 14px;
  font-family: $font-gtamerica-bold;
  white-space: normal !important;
}
.originCity {
  font-family: $font-gtamerica-bold;
  font-size: 14px;
}
.gmpaaI div:first-child {
  white-space: normal !important;
}
.fmpvlt {
  content: url("/assets/icon/grid-down-arrow.svg");
}
.eKRQOE {
  content: url("/assets/icon/grid-up-arrow.svg");
}
.gNLrQw,
.cTsoWV,
.fmpvlt {
  content: url("/assets/icon/grid-down-arrow.svg");
}
.header-md:after {
  display: none;
}

.toaster-message {
  --start: 4vw !important;
  --end: 4vw !important;
  max-height: 90%;
  text-transform: inherit !important;
  font-family: $font-gtamerica-regular !important;
  font-size: 14px !important;
  --background: #3d3d3e !important;
  // --height: 40px !important;
  --box-shadow: 0px 3px 6px #3a4c8212 !important;
  --button-color: #ff6114;
  --border-radius: 5px;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    --max-width: 81.2vw;
    margin-top: 4.54vh;
    --border-radius: 0px;
  }
}
img.loader-image {
  width: 140px;
  height: 30px;
}

.loader-message {
  .loading-wrapper.sc-ion-loading-md {
    padding: unset !important;
    width: 150px !important;
    height: 30px !important;
    --background: none !important;
    box-shadow: none;
  }
  .loading-wrapper.sc-ion-loading-ios {
    padding: unset !important;
    width: 150px !important;
    height: 30px !important;
    --background: none !important;
    box-shadow: none;
  }
}
.truck-loader-image {
  width: 100px !important;
  height: 100px !important;
}
.carrier-alert {
  .alert-wrapper {
    height: 175px;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      --min-width: 350px;
    }
  }
  .alert-head {
    padding-top: 3px;
    padding-bottom: 10px;
    font-size: 15px;
  }
  .alert-button-group {
    margin: 12px auto;
    border: 0px;
    width: 100%;
  }
  .alert-button {
    border: 1px solid #f85f18;
    width: 80px;
    margin-left: 25px;
    margin-right: 15px;
    color: black;
    font-size: 13px;
    min-width: 0px;
  }
  .alert-button.sc-ion-alert-ios:last-child {
    border: 1px solid #f85f18;
    width: 80px;
    margin-left: 15px;
    margin-right: 25px;
  }
}
.register-alert {
  .alert-button {
    border: 1px solid #f85f18 !important;
    max-width: 119px;
    color: black;
    margin: auto;
    font-size: 13px;
  }
}
