
@import "../../../theme/variables.scss";
@import "../../../theme/responsive-styles.scss";
.sc-ion-alert-ios-h{
    --min-width:91%;
}
.alert-wrapper.sc-ion-alert-ios{
    border-radius:0px;
}
.alert-head.sc-ion-alert-ios{
    padding-top:16px;
    border-bottom:1.5px solid #E9E5E1;
    font-size:16px;
    font-family: $font-gtamerica-bold;
}
.alert-input-group.sc-ion-alert-ios, .alert-message.sc-ion-alert-ios{
    padding: 25.75px 32px 10.5px 32px;
    color: #000000;
    font-size:14px;
    font-family: $font-gtamerica-regular;
}
.alert-button.sc-ion-alert-ios:last-child{
    border:0;
}
.alert-button-group.sc-ion-alert-ios{
    width: 80vw;
    height: 7vh;
    margin: 16.5px auto 3vh auto;
  .firstBtn {
        border: 1px solid #F85F18;
	}
	.secondBtn {
        border: 1px solid #F85F18;
	}
}   