.page-header-searchResults{
    width:100%;
    height:72px;
    background: #FFFFFF;
    box-shadow: 0px 3px 4px #0000000F;
    opacity: 1;
    @media only screen  and (min-width : map-get($grid-breakpoints, lg) ) {
        background: none;
        height: 120px;
        padding: 0;
        box-shadow: none;
}
}
.page-header-searchResults .header-desc {
    height: 100%;
  }
  .page-header-searchResults ion-col {
    height: 100%;
    display:table-cell;
    
  }
  .edit_icon{
      padding-top: 35px;
    float: right;
  }
  .Add_text{
    position: absolute;
    top: 36%;
font-size: 18px;
font-family:$font-gtamerica-regular;
  }
.edit_text{
    color: #000000;
    font-size: 16px;
    padding-left: 10px;
    padding-right: 16px;
  }
  .page-header-searchResults ion-col * {
    vertical-align: middle;
    display: table-cell !important;
  }
.desktop-header-desc{
 display: none;       
}
.start-trip-header{
    color: #000000;
    font-size: 18px;
    padding-left: 10px;
    padding-right: 16px;
    font-family:$font-gtamerica-regular;
}

@media only screen  and (min-width : map-get($grid-breakpoints, lg) ) {
    #header-icon {
        float: right;
        padding-top: 3vh;
        padding-right: 1.67vw;
    }
    .page-header{
        background: none;
        height: 120px;
        padding: 0;
        box-shadow: none;
    }
    .header-md:after{
        height: 0 !important;
    }
    .header-desc{
        display: none;
    }
    .desktop-header-desc{
        display: unset;
        .header-title {
            font-size: 22px;
            color: #F57423;
            text-align: left;
            font-family: $font-gtamerica-medium;
            margin: 1.92vh 0 3.36vh 0;;
          }
        .desktop-back-button {
            max-height: 16px;
            margin-top: 0px;
        }
    }
    .segment-row{
        background: #FFFFFF;
        margin-top: 0px;
    }
    
}
.desktop-back-button {
    text-transform: capitalize;
    --color: #FF6418 !important;
    --color-focused: #FF6418;
    --color-hover: #FF6418;
    --background: #eae5e2;
    --background-focused: #eae5e2;
    --background-hover: #eae5e2;
    --padding-start: 0px;
    --padding-end: 0px;
    margin-top: 2px;
    min-height: 16px;
    --box-shadow: none;
    font-size: 12px;
    background-color:#E9E5E1;
    ion-icon{
        display: none;
    }
}
.desktop-header-icon{
    padding: 10px 0 0 0;
   ion-icon{
        float: right;
        height: 24px;
        width: 24px;
    }
}
ion-back-button{
    .button-native.sc-ion-back-button-ios{
    width:30px;
}
}
.title-col{
    padding-right:0px;
}
.header-back-button{
    --background: #FFFFFF;
    --background-activated: #FFFFFF;
    --background-focused: #FFFFFF;
    --background-hover: #FFFFFF;
    --box-shadow: none;
    ion-icon{
        height: 17px;
        padding-top:37px;
    }
}
.star-icon{
    float: right;
    padding-top: 35px;
    margin-right: 19px;
    height: 20px;
    width: 20px;
}