@import '../../../../theme/variables.scss';
@import '../../../../theme/responsive-styles.scss';

.load-map {
  .map-grid {
    padding: 0;
    margin-bottom: 16px;
  }
  .gm-style-cc {
    display: none !important;
  }
  div > a > div > img {
    display: none;
  }
}

.satellite-view-mobile {
  .satellite-header {
    height: 76px !important;
    color: #f85f14 !important;
  }

  .satellite-toolbar {
    height: 9vh !important;
    color: #f85f14 !important;
    box-shadow: #0000000F 0px 3px 4px;
  }

  .satellite-title {
    position: absolute;
    width: 100vw;
    text-align: center !important;
    top: 4.7vh;

    &.ios{
      padding: 0 !important;
      position: inherit;
    }
  }
  .satellite-title {
    color: #f85f14 !important;
    font-size: 4.8vw !important;
    font-family: $font-gtamerica-bold;
  }
  .satellite-map {
    height: calc(100% - 60px);

    .map-grid {
      padding: 0;
      margin-bottom: 16px;
    }
    .gm-style-cc {
      display: none !important;
    }
    div > a > div > img {
      display: none;
    }
  }

  .satellite-close {
    padding: 1.72vh 5px 0 0;
    width: 9.9vw;
    height: 9.31vh;
    
    &.ios{
    padding: 0 1.34vw 0 0;
    width: 12.9vw;
    height: 12.31vh;
    }
  }
}
.satellite-close{
  font-weight: bold;
  color: #000;
}

.satellite-view-desktop {
  .modal-wrapper {
    width: 700px;
    height: 500px;
  }

  .satellite-header {
    height: 45px !important;
    color: #f85f14 !important;
  }

  .satellite-toolbar {
    height: 45px !important;
    color: #000 !important;
  }

  .satellite-title {
    padding: 0px 10px !important;
    color: #000 !important;
  }

  .satellite-map {
    height: calc(100% - 45px);

    .map-grid {
      padding: 0;
      margin-bottom: 16px;
    }
    .gm-style-cc {
      display: none !important;
    }
    div > a > div > img {
      display: none;
    }
  }

  .satellite-close {
    font-weight: bold;
    width: 25px;
    height: 25px;
    color: #000;
  }
}
.startTrip-map{
  height:100%;
  .load-map{
    height:100% !important;
    .map-grid{
      height:100% !important;
    }
  }
}
