@import "src/theme/variables.scss";
@import "src/theme/responsive-styles.scss";
.BookLoadCard {
  margin: auto !important;
  background: #ffffff;
  border-radius: 0;
  border: 0.5px solid #0000003d;
 
  box-shadow: 0px 3px 6px #3a4c8230;
  width: 90.9vw;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
  width: 362px;
  }
}
@media only screen and (min-width: map-get($grid-breakpoints, lg)) {
  .load-btn{
      width: 132px !important;
      height:40px !important;
      white-space: break-spaces !important;
  }
}

.BookLoadCard .start-trip-cardContent {
    color: #000000;
    font-size: 16px;
    font-family: $font-gtamerica-bold;
    text-align: center;
    padding-top: 15.5px;
    padding-bottom: 11px;
    border-bottom: 1.5px solid #E9E5E1;
}
.book-load-p {
  font-size: 13px !important;
  font-family: $font-gtamerica-regular;
  color: #000000;
  padding-left: 10.5px;
  padding-right: 20.5px;
  padding-top: 18px;
  padding-bottom: 18px;
  margin: 0;

  // @media (min-width: map-get($grid-breakpoints, lg)) {
  //   padding-left: 1vw;
  // padding-right: 0.5vw;
  // }
}
.book-card-content {
  padding: 0;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    padding-left: 11px;
    padding-right: 10px;
  }
}

.book-load-cardContent {
  padding-left: 0px;
  padding-right: 0px;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    95deg,
    #f3f1ef 35%,
    #f3f1ef 35%,
    #f3f1ef 35%,
    #ffffff 33%,
    #ffffff 10%
  );
}
.accept-btn-row {
  width: 100%;
  padding-left: 32.5px;
  padding-right: 30px;
  padding-bottom: 4%;
}
.Total-count {
  text-align: right;
  padding-right: 9px;
}
.book-load-row {
  border-bottom: 1px solid #dedede !important;
  font-size: 15px;
  font-family: $font-gtamerica-medium;
  color: #000000;
  padding: 12px 3px 26.29px 7.5px;
  }
.book-load-col {
  text-align: right !important;
}
.book-load-col2 {
  text-align: center !important;
}
.book-load-col3 {
  padding-left: 0;
  padding-right: 0;
}
.book-header {
  padding-bottom: 0;
  padding-left: 7.5px;
}
.book-header-border {
  border-bottom: 1px solid #dedede;
}
.bookLoad-thankyou {
  margin: auto !important;
  background: #ffffff;
  border-radius: 0;
  border: 0.5px solid #0000003d;
  
  box-shadow: 0px 3px 6px #3a4c8230;
  width: 90.5vw;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    width: 342px;
  }
 
}
.bookload-thanku-p {
  color: #000000;
    padding: 22.5px 30px 15px 30px;
    font-family:$font-gtamerica-regular;
    text-align: center;
}
.bookload-OK {
  text-align: center;
}
.rate-card-scroll {
  padding: 0;
  overflow-y: scroll;
  max-height: 165px;
}
.book-load-thankyou-row{
  padding-bottom: 4%;
}
