.select-grid {
    background: white;
    margin-bottom: 10px;
    padding: 0px;
    position: relative;
    margin-top: 10px;
}

.select-label-row {
    height: 25px;
    padding-left: 10px;

    .select-label {
        color: #000;
        font-family: "GT America Regular" !important;
        font-size: 15px;
        margin-top: 0px;
        height: 25px;
    }

    
}

.select-type {
    border-bottom: 1.5px solid #e9e5e1;
    font-family: "GT America Medium";
    padding-left: 10px;

    .select-name {
        color: #000;
        font-weight: 500;
        font-size: 15px;;
        padding-bottom: 5px !important;
    }
    .dropDown-icon {
        text-align: end;
        padding-right: 21px !important;
    }
}
.select-suggetion-grid
{
    width: 100%;
    padding: 5px 0 5px 10px !important;
    height: 150px;
    background: #fff;
    overflow: auto;
    z-index: 1;
    .select-suggestion-item {
        padding: 5px 0 !important;
        border-bottom: 1px solid #eee;
    }
}