@import "../../../../theme/variables.scss";
@import "../../../../theme/responsive-styles.scss";

.add-capacity-content,
.add-capacity-form-content {
  --background: #e9e5e1 !important;
}

.add-capacity-form-content {
  --padding-bottom: 50px;
  .trailer-type-card {
    padding: 12px 8px;
    --background: #ffffff;
    border-radius: 0;
    z-index: 10;
    margin: 0px;
    box-shadow: 0px 3px 6px #645a531a;
  }
  .add-capacity-grid-mobile {
    padding: 0px;
    position: relative;
    z-index: 1;
  }
  .add-capacity-pick-up {
    padding: 0px;
    background-color: #ffffff;
    margin-top: 2px;
    box-shadow: 0px 3px 6px #645a531a;
    .pickup-row {
      padding-top: 18px;
      padding-left: 11px;
      font-size: 15px;
      font-family: "GT America Bold";
      margin-bottom: 6px;
    }
    ion-col {
      padding: 0px;
    }
    .typeahead-item {
      width: 100%;
      margin-left: 0;
      .destination-input_icon {
        width: 10px;
        height: 10px;
        margin-right: 17px;
        margin-top: 52px;
      }
    }
    
  }
  .ion-item1 {
    height: 85px !important;
  }
  .delivery-type-card {
    box-shadow: none;
    border-radius: 0;
    margin: 0px;
    border: none;
    ion-card-header {
      font-family: $font-gtamerica-bold;
      color: #000000;
      font-size: 15px;
      padding-left: 10px;
      .desitnation-city {
        width: auto;
        float: right;
        font-family: $font-gtamerica-regular;
      }
      ion-icon {
        float: right;
        height: 8px;
        width: 8px;
        margin-top: 5px;
        margin-left: 10px;
      }
    }
    .pickupdate-error:last-child {
      height: auto;
    }
    ion-card-content,
    ion-grid,
    ion-col {
      padding: 0;
    }
    .typeahead-item {
      margin-top: -15px;
      margin-left: 0;
    }
    .ion-item1 {
      height: 95px !important;
    }
  }
  .pickRadius {
    padding-bottom: 10px;
    font-size: 13px;
    ion-label {
      font-size: 13px;
    }
    .value-div {
      text-align: right;
      margin-top: -13px;
    }
  }
  ion-label {
    color: #645a53;
    font-family: "GT America Regular" !important;
    font-size: 15px;
    margin-top: 17px;
    height: 25px;
  }
  ion-datetime {
    --padding-top: 0px;
    --padding-bottom: 0px;
    --placeholder-color: #000;
    --padding-start: 0px;
    font-size: 15px;
    margin-bottom: -9.5px;
    font-family: "GT America Bold";
    color: #000;
  }
  .delivery-data {
    padding-bottom: 20px !important;
  }
  .time-window {
    border-bottom: 1.5px solid #e9e5e1;
    padding: 15px 10px 20px 10px;
    ion-label {
      font-size: 13px;
      color: #7d736d;
    }
    .to-date-grid {
      float: right;
      padding: 0;
    }
  }
  .capacity-grid {
    background: white;
    height: 100px;
    margin-bottom: 20px;
    padding: 0px;
    .capacity-label {
      font-size: 13px;
    }
    ion-col {
      padding: 0px;
      padding-bottom: 5px;
    }
    ion-row {
      padding-left: 2.4vmin;
    }
    .capacity-label-row {
      height: 35px;
    }
    .capacity-type {
      border-bottom: 1.5px solid #e9e5e1;
      font-family: $font-gtamerica-medium;
      padding-top: 4px;
      height: 26px;
      margin-bottom: 5px;
    }
    .capacity-name{
      padding-bottom: 0px;
    }
    .capacity-desc {
      font-size: 13px;
      color: #645a53;
    }
    .dropDown-icon {
      text-align: end;
      padding-right: 21px;
      margin-top:-8px;
      ion-icon {
        height: 6px;
        margin-bottom: -4px;
      }
    }
  }
}
.suggetion-grid {
  box-shadow: 0px 3px 6px #3a4c8230;
  width: 100%;
  border: 0.5px solid #00000021;
  background: white;
  z-index: 9;
  position: absolute;
  margin-top: -24px;
  padding: 0 21px 0 37px;
  ion-row {
    height: 38px;
    font-size: 12px;
    color: #666666;
    padding-top: 12px;
    padding-left: 0px !important;
  }
  ion-row:first-child {
    border-bottom: 0.5px solid #0000003f;
  }
}
.addTruckFooter {
  ion-button {
    width: 100%;
    height: 56px;
    position: fixed;
    text-transform: none;
    margin-top: -51px;
    font-family: $font-gtamerica-bold;
    font-size: 18px;
    margin-left: 0;
  }
  @media (min-width: map-get($grid-breakpoints, lg)) {
    display: none;
  }
}
.modal-wrapper.sc-ion-modal-md {
  @media (min-width: map-get($grid-breakpoints, lg)) {
    box-shadow: none;
  }
}
.delivery-type-card {
  .states-list-wrapper {
    margin-top: 35px;
    &.error {
      .select-grid {
        .select-type {
          border-color: red !important;
        }
      }
    }
  }
  ion-text.states-toggle-wrapper{
    position: absolute;
    z-index: 1;
    right: 0;
    width: 100%;
    .destination-wrapper {
      float: left;
      span {
        color: #000;
        font-family: "GT America Regular" !important;
        font-size: 15px;
        height: 25px;
        padding: 0 10px;
        display: block;
        line-height: 25px;
      }
    }
    .state-wrapper {
      float: right;
      span {
        font-size: 15px;
        float: left;
        color: #645a53;
        line-height: 25px;
        padding: 0 10px;
      }
    }
    
  }
  .state-toggle {
    --background: #f2f2f2;
    --background-checked: #f2f2f2;
    --handle-background: #9c9089;
    --handle-background-checked: #f85f14;
    zoom: 0.8;
    margin-right: 12px;
  }
  .typeahead-item {
    width: 120%;
  }
}

.manage-capacity-wrapper {
  margin: 20px 5px 0;
  margin-bottom: 20px;
  padding: 0 15px 10px;
  background: #fff;
  h3 {
    margin: 0;
    background: #fff;
    padding: 17px 2px 5px;
    font-size: 15px;
    font-family: $font-gtamerica-bold;
    position: relative;
    &.underline {
      border-bottom: 1px solid #e9e5e1;
      width: 85vw;
    }
    span {
      position: absolute;
      right: 5px;
      font-size: 15px;
      color:#000000;
      font-family: $font-gtamerica-medium;
    }
  }
}

.manage-input-field {
 font-family: $font-gtamerica-medium;
  font-size: 14px;
  height: 25px;
  --padding-bottom:0px;
}
.manage-capcity-list{
  margin-top:0px;
}
.manage-capacity-item{
  margin-left: -13px;
  --min-height: 60px;
  ion-label{
    font-family: $font-gtamerica-regular !important;
  font-size: 15px !important;
  margin-top:20px !important;
  color: #000000 !important;
  }
}

.manage-capacity-buttons {
  text-align: right;
  margin: 20px 0;

  ion-button {
    margin-right: 15px;
  }
}
.manage-capacity-view-desktop {  
  .custom-padding  {
    --background: #F8F8F8 !important;
    h1 {
      text-align: center;;
      background-color: #fff;
      font-size: 16px;
      margin: 0 -20px;
      padding: 15px;
      font-weight: bold;
    }
    .manage-capacity-wrapper {
      padding-top: 10px;
      padding-bottom: 0;
      h3 {
        max-width: 100%;
        border: none;
        padding: 10px 0;
      }
    }
    .manage-capacity-item {
      width: 48%;
      display: inline-block;
      margin: 0 10px 10px;
      ion-label {
        margin-top: 0 !important;
      }
    }
    .capacity-label-row {
      padding-left: 10px;
    }
    .capacity-name{
      padding-bottom: 0px;
      padding-top: 0px;
    }
    .capacity-type {
      padding-left: 10px;
    }
    .capacity-desc {
      padding-top: 12.5px;
      padding-left: 10px;
    }
    .dropDown-icon {
      ion-icon {
        margin-bottom: -4px;
      }
    }
  }

  .add-capacity-form-content {
    h1 {
      margin-bottom: 15px;
    }
    .capacity-grid-form {
      margin: 20px -20px 0;
      background: #fff;
      padding-bottom: 20px
    }
    .pickup-row {
      background-color: #F3F1EF;
      padding: 10px 25px;
      font-weight: bold; 
      margin: -10px -10px 0;
      font-size: 16px;;
      .trailer-type-card
       {
         box-shadow: none;
         background: none;
         padding: 0;
         ion-grid {
           padding: 0;
           margin-top: -5px;
           ion-label {
             position: relative;
             top: 5px;
           }
           ion-button {
             background: none;
             position: relative;
             top: 2px;
           }
         }
       }
    }
    .pickup-content {
      padding: 20px;
      position: relative;
      .state-destination-label {
        font-size: 13px;
        padding: 15px;
        color: #645a53;
      }
      .states-wrapper {
        position: absolute;
        width: 100%;
        z-index: 1;
        .state-toggle-wrapper {
          text-align: right;
        }
      }
    }
    .pickRadius {
      ion-label {
        position: relative;
        top: -10px;
        left: 0px;
      }
    }
    .state-toggle-label {
      font-size: 13px;
      color: #645a53;
      position: relative;
      top: -7px;
      left: -10px;
    }
    .state-toggle {
      position: relative;
      top: 5px;
    }
    .delivery-data {
      ion-label {
        margin: 5px 0;
      }
    }
    .states-list-wrapper {
        margin-top: 50px;
        .select-label {
          font-size: 13px;
          padding: 5px;
          color: #645a53;
        }
    }
    .pickupdays-title {
      font-size: 13px;
      padding: 15px !important;
    }
    .pickuptime-title {
      font-size: 13px;
      padding: 7px;
      color: #645a53;
    }
    .pickup-date-title {
      padding: 0 2px;
      display: block;
      margin: 0;
      line-height: 21px;
      height: 30px;
    }
    .save-btn {
      --background: #FF6418;
      --color: #fff;
      &:hover {
        --background-hover: #FF6418;
        --color: #fff;
      }
    }
  }
}