/** responsive break points **/

$grid-breakpoints: (
  xs: 320px,
  // Phone (0px - 575px)
    //sm: 576px, // Phablet (576px - 767px)
    md: 768px,
  // Tablet vertical (768px - 991px)
    lg: 1200px,
  // Tablet horizontal, Desktop (992px and above)
    xr: 828px,
  // Iphone Xr
);
$grid-height-breakpoints: (
  xs: 568px,
  // Phone (0px - 575px)
    sm: 667px,
  md: 771px,
  // Tablet vertical (768px - 991px)
);
/*
$grid-max-widths: (
 //lg:992px
); */

// Desktop
ion-header,
ion-tab-bar,
ion-list,
ion-content,
ion-toolbar {
  margin: auto;
}

@media (min-width: map-get($grid-breakpoints, lg)) {
  ion-header,
  ion-tab-bar,
  ion-list,
  ion-toolbar {
    margin: 0;
    padding-right: calc((100% - #{map-get($grid-breakpoints, lg)}) / 2);
    padding-left: calc((100% - #{map-get($grid-breakpoints, lg)}) / 2);
  }
  ion-tab-bar {
    position: absolute;
    width: 65.24vw !important;
    height: 60px !important;
    --background: transparent !important;
    box-shadow: none !important;
    top: -97px;
    border: none;
    max-width: 65.24vw !important;
    padding-left: 0;
    padding-right: 0;
    margin-left: 0 !important;
    align-self: center;
    padding-top: 28px;
    ion-tab-button {
      max-width: 9.5vw;

      color: #000000 !important;
      padding-top: 0px;
    }
  }
  ion-content {
    --background: #f8f8f8;
  }
}
ion-content {
  background-color: #e9e5e1;
}
.pull-to-refresh {
  z-index: 100000;
  top: 1%;
  transform: scale(0.45) translateY(-1%);
}
.refresher-pulling,
.refresher-refreshing {
  display: block;
}

