@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";

.recent_container{
    --padding-start: 4vw;
    --padding-end: 4vw;
    --padding-top:0px;
    font-family: $font-gtamerica-regular;
    --background: #E9E5E1;
    .recentDesktop{
        display: none;
    }
    .display-text-grid{
        padding-top:17px;
        padding-bottom:20px;
    }
    @media (min-width: map-get($grid-breakpoints, lg)) {
        --padding-start:0;
        --padding-end:0;
        --background: #ffffff;
        .recent-list{
            padding: 0 1.78vw 1.17vh 1.78vw;
            background-color: #f8f8f8;
            display: none;
        }
        .recentDesktop{
            display: block;
        }
        .end_of_list{
            display:none;
        }
    }
    .recent-list{
        padding: 0 0 1.17vh 0;
    }
    .ceqEhh div:first-child{
        white-space: normal !important;
      }
.trailerType, .capacity-type{
    padding: 0px 0px 0 5px;
    font-size: 13px;
    font-family: $font-gtamerica-bold;
    color: #645A53;
}
.capacity-type {
    text-transform: capitalize;
}

.trailer-type {
    font-size: 13px;
    line-height: 16px;
    font-family: "GT America Bold";
    color: #645A53;
    padding: 0px 0px 0 5px;
}

.manage-trailerType{
    padding: 2px 0px 0 5px;
    font-size:13px;
    font-family: $font-gtamerica-medium;
    color:#645A53;
    margin-top:-2px;
}
.recent_grid{
    padding-bottom: 0px;
    padding-top:17px;
}
.originRadius{
    padding:2px 4px;
    font-size:13px;
    font-family: $font-gtamerica-regular;
    color: #645A53;
    border-radius: 0px;
    height:16px;
    background-color:#D6D1CA4D;
    margin-bottom:20px;
}
.pickupDate{
    padding: 1px 4px 2px 4px;
    font-size: 13px;
    font-family:$font-gtamerica-regular;;
    color: #645A53;
    margin-left: 11px;
    border-radius: 0px;
    height:16px;
    background-color:#D6D1CA4D;
    margin-bottom:20px;
}
.stops_border_recent{
    height:41px;
    margin-left:4.5px;
}
}
.recent-card{
    .stops_border{
        height: 54px;
    }
    .stop-details-row:last-child{
        margin-top: -8px;
    }
}
.star_icon_col{
    padding-top:0px;
    margin-top:-6px;
}


