@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";


.search-result-page{
    .ellipse-col{
        padding-top:15px;
        margin-left:10px;
    }
    .stops_border{
        height:76px;
        margin-left:11px;
        margin-top:0px;
       
    }
    .ellipse_icon{
        margin-left: 9px;
    }
    ion-back-button{
        padding-top:27px;
        ion-icon{
            width:17px;
            height:17px;
        }
    }
    .stop-details-row:last-child{
        .stop-details-col{
            margin-top: -3px;
        }
    }
}
.search_results_container{
    --padding-start: 4.13vw !important;
    --padding-end: 4.13vw !important;
    --background: #E9E5E1;
    ion-col{
        padding: 0;
    }
    .searchResults_list{
        background: #E9E5E1;
    }
    .ion-segment{
        margin-top: 10px;
         border: none;
        height: 3.46vh;
        // margin-left: 4.13vw;
        ion-segment-button{
            margin-top: -0.53vh;
            --background-checked:none;
            opacity: 0.84;
            --padding-start:0;
            --padding-end:2.67vw;
            border: none;
            @media (max-height: map-get($grid-height-breakpoints, xs)) {
                margin-top:0.5vh;
            }
            @media (max-height: map-get($grid-height-breakpoints, sm)) {
                margin-top:-1.2vh;
            }
            @media (max-height: map-get($grid-height-breakpoints, md)) {
                margin-top:-1.3vh;
            }
        }
    }
    .search_floating_badge {
        background-color: #f1eded;
        color: #645A53;
        font-size: 3.2vw;
        font-family: "GT America Regular";
        text-transform: capitalize;
        border-radius: 0;
        padding-bottom: 1vh;
        &.ios{
            padding-top: 0.37vh;
            padding-bottom: 0.37vh;
        }
    }
    .sort_container{
        font-family: $font-gtamerica-regular;
        color: #666666;
        .rec_text{
            font-size: 13px;
            padding-top: 0.61vh;
        }
        .sort_select{
           padding-bottom: 0.61vh;
          
        }
    }
    .short-div{
        
        padding: 0 0 5px 0;
        margin-bottom:15px;
        .short-row{
            padding: 0;
            margin-bottom: 10.5px;
            margin-top: 25px;
        }
    }
    .search-desktop-view{
        display: none;
    }
    @media ( min-width: map-get($grid-breakpoints, lg)) {
        --padding-start: 0 !important;
        --padding-end: 0 !important;
        .ion-segment,.short-div{
            display: none;
        }
        .search-desktop-view {
            display: flex;
            font-family: $font-gtamerica-regular;
            color: #666666;
            height: 50px;
            padding-top: 16px;
            padding-left: 1.46vw !important;
            padding-right: 1.46vw !important;
            background-color: #EDEDED;
            .rec_text{
                font-size: 13px;
                padding-top: 0.61vh;
            }
            ion-segment{
                height: 2.5vh;
                margin-top: 5px;
                margin-right: 2vw;
                ion-segment-button {
                    min-height: 20px !important;
                }
            }
            ion-img{
                width: 22px;
                margin-left: 15px;
                margin-top: 5px;
            }
            .search_floating_badge{
                padding:0 1px;
            }
        }
        ion-list{
            padding-top: 3px;
            padding-left: 1.46vw !important;
            padding-right: 1.46vw;
            background-color: #f8f8f8;
        }
    }
}
@media ( min-width: map-get($grid-breakpoints, lg)) {
    .search-result-page .page-header{
        height: 8.5vh;
    }
}
.end_of_list{
    font-size:13px;
    color: #645A53;
    font-family: $font-gtamerica-regular;
    width:165px;
    position: relative;
    margin:20px auto 20px auto;
    text-align: center;
    padding: 0px 0 0 0px;
}
.loads-div{
    margin-bottom:-24px;
}