@import "../../../../theme/variables.scss";
@import "../../../../theme/responsive-styles.scss";
@import "../FilterWithSort/FilterWithSort.scss";

.filter-display-text {
    padding: 0;
    font-size: 14px;
    ion-icon {
        position: relative;
        top: 3px;
        left: 4px;
    }
}

.list-items-filter {
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        height: 40px;
        padding-right: 15px;
     }
}
.list-items-header {
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        margin: 0;
     }
}

.list-item-button {
    text-align: left;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        text-align: right;
        margin-bottom: 0;
    }
}