@import '../../../../theme/variables.scss';
@import '../../../../theme/responsive-styles.scss';

.deviceInfo-item{
    padding-right: 15px;
    margin-left: -5px;
    --inner-padding-end: 5px;
    --min-height:48px !important;
    --border-color: #0000003f;
}
.deviceInfo-row{
    width:100%;
    height:100%;
}
.deviceInfo-col{
   
    padding-left:0px;
    padding-right:0px;
}
.deviceInfo-p{
    font-family: $font-gtamerica-regular;
    font-size: 15px;
    color: #000000;
    padding: 0px;
}
.deviceInfo-version{
    float:right;
}
.deviceInfo-item:last-child {
    --border-color: white;
}