  border: 0.5px solid #00000021;
  background: white;
  z-index: 9;
  position: absolute;
  width: 100%;
  ion-row {
    padding: 5px 0px;
  }
}

.actual-start-time.datetime-disabled {
  color: #645a53 !important;
  opacity: 1 !important;
  font-weight: normal !important;
  font-size: 15px !important;
  margin-top: -5px !important;
}

.shipment-dialog {
  .modal-wrapper {
    min-width: 300px;
    max-width: 92%;
    max-height: 185px;
    text-align: justify;
    .ion-page {
      background-color: #ffffff;
    }
  }
  header {
    text-align: center;
    padding: 15px;
  }
  section {
    max-height: 250px;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 20px;
    .ion-card-updated {
      margin-bottom: 0px !important;
    }
  }
  footer {
    text-align: center;
    padding-bottom: 20px;
    padding-top: 20px;
    .btn-Yes,
    .btn-No {
      height: 42px;
      --background: #ffffff;
      --color: #000000;
      font-size: 13px;
      --border-radius: none;
      --box-shadow: none;
      border: 1px solid #f85f14;
      font-family: "GT America Medium";
      margin: 0;
      --background-focused: #ffffff;
      --background-hover: #ffffff;
      --background-activated: #ffffff;
      --color-activated: #000000;
      --color-focused: #000000;
      text-transform: none;
      width: 45%;
    }
  }
  .btn {
    width: 132px;
  }
}
