@import  "../../../theme/variables.scss";
@import  "../../../theme/responsive-styles.scss";

.custom-modal {
    .modal-wrapper {
        width: 92vw;
        max-height: 27vh;
        min-height: 223px;
        .ion-page {
            background-color: #ffffff;
        }
        @media (min-width: map-get($grid-breakpoints, lg)) {
           width: 20vw;
        }
    }
    .custom-modal-header {
        text-align: center;
        padding: 15px;
        border-bottom: 1px solid #E9E5E1;
        .custom-modal-title {
            font-weight: bold;
            font-size: 16px;;
        }
    }
    section {
        max-height: 200px;
        overflow-y: scroll;
        overflow-x: hidden;
        text-align: center;
        .ion-card-updated {
            margin-bottom: 0px !important;
        }
        .custom-modal-message {
            padding: 20px 24px;
            box-sizing: border-box;
            width: 100%;
            display: block;
            font-size: 14px;
            line-height: 18px;
            span.sub-heading {
                font-size: 16px;
                display: block;
            }
        }
    }
    .btn {
        width: 132px;
    }
    footer {
        text-align: center;
        padding-bottom: 22px;
    }
}