@import "../../../../theme/variables.scss";
@import "../../../../theme/responsive-styles.scss";
.card-price-col {
  padding: 0px;
}
.card_p-elements {
  padding: 0px;
  font-size: 13px !important;
  color: #645a53;
  font-family: $font-gtamerica-regular;
}
.card-price {
  text-align: left;
  padding: 0px;
  letter-spacing: 0;
  color: #000000;
  opacity: 1;
  font-family: $font-gtamerica-bold;
  font-size: 22px !important;
  margin-bottom: 4px !important;
  @media only screen and (device-aspect-ratio: 40/71) {
    font-size: 20px !important;
  }
}
.loadno_card {
  color: #000000;
  margin-top: 1vh !important;
  font-family: $font-gtamerica-bold;
}
.card-contact_for_price {
  font-size: 16px !important;
  padding: 0px;
  text-align: left;
  letter-spacing: 0;
  color: #000000;
  opacity: 1;
  font-family: $font-gtamerica-bold;
  padding-right: 24px;
  line-height: 17px;
}
.load_header_elements {
  padding: 0px;
  font-size: 13px !important;
  color: #645a53;
  font-family: $font-gtamerica-regular;
}
.card-distance {
  font-family: $font-gtamerica-medium;
  padding: 0px;
  font-size: 13px !important;
  color: #645a53;
  margin-top: 1vh !important;
}
.no-margin {
  margin-top: 0px !important;
}
.card-stopOffs {
  font-family: $font-gtamerica-medium;
  padding: 0px;
  font-size: 13px !important;
  color: #645a53;
}
.card-city-col {
  padding: 0px;
}
.origin_city {
  color: #000000;
  font-size: 16px !important;
  font-family: $font-gtamerica-bold;
  padding: 0px;
}
// .origin_milehead{
// }
.origin_dateTime {
  font-family: $font-gtamerica-medium;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding-bottom: 21px !important;
    // border-bottom: 1px solid #E8E8E8;
  }
}
.stop-address-pad {
  padding-top: 11px;
}
.ellipse-row {
  width: 5px;
  height: 5px;
}
.ellipse_icon {
  width: 5px;
  height: 5px;
  margin-left: 11px;
  margin-top: 10px;
}
.ellipse-col {
  padding-top: 15px;
  padding-left: 8px;
}
.stops_border {
  width: 0px;
  height: 73px;
  border: 1px solid #f3f1ef;
  margin-left: 12.4px;
  // margin-top:-10px;
}
.path_icon {
  width: 6px;
  height: 9px;
  margin-left: 10px;
}
.star_icon_col {
  padding: 3px 0 0 0;
}
.star_icon {
  width: 16.6px;
  height: 15.8px;
}

.stop-details-grid {
  padding: 0;
  margin-top: -5px;

  .stop-details-row {
    margin-top: -8px;
  }
  .stop-details-row:first-child {
    margin-top: 0px !important;
    .stop-details-col {
      margin-top: 0px !important;
    }
  }
}
.stop-icon-col {
  max-width: 1px;
  padding: 0;
  text-align: right;
}
.stop-details-col {
  padding: 0;
  margin-left: 20px;
}
.assign-load-details-grid {
  padding: 0px;
  .stops_border {
    height: 50px;
  }
  .stop-details-row {
    .stop-details-col {
      margin-top: -7px;
      .origin_city {
        margin: 0;
        height: 20px;
      }
    }
  }
  .stop-details-row:first-child {
    .stop-details-col {
      margin-top: 0px;
    }
  }
}
.assign-driver-row {
  margin-left: 6px;
  .assign-driver-icon-col {
    max-width: 24px;
    padding: 14px 0;
    max-height: 24px;
  }
  .change-driver-col {
    max-width: 28px;
    padding: 11px 0;
    max-height: 28px;
  }
  .chnages-driver-icon-div {
    background: #f85f14;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    ion-icon {
      height: 16px;
      width: 16px;
    }
  }
  .assign-driver-icon-div {
    background: #f85f14;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    padding: 2px 3.5px 6.94px 5px;
  }
  .label-col {
    padding: 16px 0 0 6px;
    color: #000000;
    font-size: 13px;
    font-family: $font-gtamerica-regular;
  }
}
.plus-icon {
  height: 10px;
  width: 10px;
  margin-left: 10px;
}
.driver-name-div {
  background: linear-gradient(97deg, #f85f14 50%, #7d7169 50%);
  border-radius: 50%;
  height: 32px;
  width: 33px;
  bottom: 0;
  position: absolute;
  margin: 0;
  color: #ffffff;
  text-align: center;
  font-size: 13px;
  font-family: $font-gtamerica-medium;
  padding: 7px 0 5px 3px;
  letter-spacing: 4px;
}
.complete-address {
  padding: 0;
  p {
    font-size: 13px;
    color: #645a53;
    padding: 0;
    line-height: 14px;
    margin: 0px;
  }
  p:last-child {
    margin-bottom: 11px;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    margin-top: 10px;
  }
}
.stops_border-expanded {
  height: 130px;
}

.stop-data-grid,
.stop-grid {
  padding: 0px;
  border-left: 1px solid #f3f1ef;
  margin-left: 10px;
  padding-bottom: 23px;
  .path_icon {
    margin-left: -2px;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding: 30px 0 0 0;
  }
}
.stop-data-grid:first-child {
  margin-top: 12px;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding: 0px;
  }
  .stop-address-grid {
    margin-top: -13.5px;
  }
}
.stop-data-grid:last-child {
  padding-bottom: 0px;
  border-left: none;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    border-left: 1px solid #f3f1ef;
    border-image: linear-gradient(
      to top,
      rgba(243, 241, 239, 0) 69%,
      rgba(243, 241, 239, 1) 25%,
      rgba(243, 241, 239, 1) 98%,
      rgba(243, 241, 239, 0) 75%
    );
    border-image-slice: 1;
  }
}
.stop-icon {
  width: 5px;
  height: 5px;
  margin-left: -3px;
}
.stop-address-grid {
  padding: 0 0 0 5px;
  margin-top: -15.5px;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding: 0px;
    border-bottom: 1px solid #dedede;
    margin-left: 20px;
  }
}
.location-pointer-change {
  color: #000000;
}

@media only screen and (min-width: map-get($grid-breakpoints, lg)) {
  .load-tile-row {
    margin-left: 26px;
    border-bottom: 1px solid #dedede;
    ion-col:first-child {
      margin-top: 0px;
    }
    ion-col {
      padding: 0;
      margin-top: -10px;
    }
  }
}
