@import '../../../theme/variables.scss';
@import '../../../theme/responsive-styles.scss';
.desktop-page {
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    .myLoads_div_desktop {
      margin-top: -11px;
      width: 110.5%;
    }
    .myload-content {
      --padding-top: 51px;
    }
    .myload-tab-card {
      height: 190px;
      width: 216px;
      margin-bottom: 0px;
    }
    .thumbnail-img {
      margin-top: 12px;
    }
    .manage-financial-div {
      margin-top: 20px;
      width: 107.5%;
    }
    .myLoads_row,
    .myload-mobile-grid {
      display: none;
    }
    .myLoads_row_desktop {
      display: block;
      display: flex;
    }
    .circle-div {
      top: 16px;
      right: 0;
    }
    .carrier_assigned {
      padding-top: 0px;
    }
    .driver_assinged {
      padding-top: 0px;
    }
    .in-transit {
      padding-top: 0px;
    }
    .delivered {
      padding-top: 0px;
    }
    .recently_delivered {
      padding-top: 0px;
    }
    .myload-content {
      --padding-start: 0px;
    }
    .thumbnail-item-col:nth-child(even) {
      .myload-tab-card {
        float: none;
      }
    }
  }
}
.thumbnail-img {
  width: 36px;
  left: 50px;
}
.myLoads_row_desktop {
  display: none;
}
.carrier_assigned {
  padding-top: 0px;
  .thumbnail-img {
    height: 56px;
  }
}
.driver_assinged {
  padding-top: 0px;
  .thumbnail-img {
    height: 61.5px;
  }
  .itemlabel {
    padding: 12px 17px 6px 17px;
  }
}
.in-transit {
  padding-top: 0px;
  .thumbnail-img {
    height: 37.58px;
  }
}
.delivered {
  padding-top: 0px;
  .thumbnail-img {
    height: 49.47px;
  }
}
.recently_delivered {
  padding-top: 0px;
  .thumbnail-img {
    height: 50.13px;
  }
}
.manage-img {
  float: left;
  width: 52px;
  height: 32px;
  margin-top: -6px;
}
.manage-financial {
  width: auto;
  height: 57px;
  background: #ffffff;
  border: 1px solid #ff6418;
}
.manage-financial-div {
  height: 83px;
  margin-top: 67px;
}
.itemlabel {
  width: auto;
  padding: 12px 13px 0px 15px;
  text-align: center;
  font-size: 16px;
  font-family: $font-gtamerica-bold;
  color: #000000;
}
.manage-itemlabel {
  margin: auto;
  width: 223px;
  margin-top: 13px;
  p {
    width: auto;
    margin-top: 12px;
    padding: 0px 9px 6px 10px;
    text-align: center;
    font-size: 16px;
    font-family: $font-gtamerica-bold;
    color: #000000;
  }
}
.itemlabel-loads {
  padding: 4px;
}
.thumbnail-item-card {
  width: 140px;
  height: 140px;
  //   margin: 10px;
}

.thumbnail-item-col {
  // width: 132px;
  padding: 0px !important;
  margin-right: 23px;
}

.circle-div {
  position: absolute;
  right: 8px;
  top: 7px;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  font-family: $font-gtamerica-regular;
  background: #f85f14;
  color: #ffffff;
  text-align: center;
  padding-top: 2px;
  font-size: 16px;
}

.circle-div:empty {
  display: none;
}
.count {
  text-align: center;
  position: absolute;
  top: 20px;
  left: 20px;
  width: 20px;
}

.item-dev {
  width: 155px;
  max-width: 100%;
  position: relative;
  height: 100%;
  display: flex;
  padding: 0;
}

.icon-wrapper {
  margin: auto;
}

.myload-tab-card {
  height: 144px;
  width: 47%;
  box-shadow: 0px 3px 6px #645a531a;
  border-radius: 0px;
  background-color: #ffffff;
  margin: 0;
  margin-bottom: 4.2666vw;
}
.thumbnail-item-col:nth-child(even) {
  .myload-tab-card {
    float: right;
  }
}
.myload-content {
  --padding-top: 2.56vh;
  --background: #e9e5e1;
}
.myload-page-content{
  --background: #e9e5e1;
  --padding-start: 6.1vw;
  --padding-end: 6.1vw;
}
.myload-mobile-grid {
  padding: 0;
  max-width: 328px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.myLoad-landing-page {
  ion-header {
    height: 72px;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    ion-header {
      height: 0px;
      margin-top: 0px;
    }
    .header-title {
      display: none;
    }
  }
}
.card-disabled {
  opacity: 1 !important;
}
