@import "../../../theme/variables.scss";
@import "../../../theme/responsive-styles.scss";

.manageHeader {
  background: linear-gradient(180deg, #fca364, #fd7d4f);
  height: 29.3vh;
}
.profileImg {
  width: 30.8vw;
  height: 30.8vw;
  border-radius: 50%;
  margin-top: 89px;

  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    width: 111px;
    height: 111px;
    margin-top: 39px;
  }
}
.profile-cardContent {
  padding-left: 4vw;
  padding-right: 4.24vw;
}
.profileHeader {
  text-align: center;
  padding-top: 5.05vh;
  //font-family:$font-roboto-regular;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    margin-top: 11.6vh;
  }
  .editBadge {
    width: 30px;
    height: 30px;
    background-color: #ffff;
    border-radius: 50px;
    border: 1px #ffff;
    margin: 0px 0px 4px -29px;
  }
  .profileEditIcon {
    width: 13.63px;
    height: 13.61px;
    margin-top: 6px;
  }
}
.profileName {
  font-size: 18px;
  color: #000000;
  font-family: $font-gtamerica-bold;
  padding-bottom: 10px;
  white-space: break-spaces;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding-bottom: 5px;
  }
}
.card_content_background {
  background-color: #fd7d4f;
  ion-col {
    padding: 0;
  }
}
.profile-role {
  font-size: 13px;
  padding: 5px 0;
  color: #645a53;
  font-family: $font-gtamerica-regular;
  border-bottom: 1px solid #c0c0c0;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    border: none;
    font-size: 15px;
    padding: 0;
  }
}
.profile-add-roles {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
        padding: 5px 0;
        font-size: 13px;
    }
}
.profileEmail {
  font-size: 15px;
  color: #645a53;
  font-family: $font-gtamerica-regular;
  padding: 5px 0;
}
.profileImg-col {
  text-align: end;
  margin-top: 89px;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    margin: 10px 0 30px;
  }
}
.managesideimg {
  width: 6px !important;
}
.profile-Col {
  margin-top: 112px;
  text-align: left;
  padding-left: 22px;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding-left: 20px;
    margin-top: 35px;
  }
}
.profileCard {
  height: 9.85vh;
  margin: 0.86vh 2.67vw;
  box-shadow: 0px 3px 6px #3a4c8212;
  border: 0.5px solid #dedede;
  color: black;
  .carrier-profile {
    font-size: 16px;
    color: #ff6418;
    margin-top: 3.5vh;
    padding: 0;
    opacity: 0.84;
  }
}
.cardContent-space {
  //font-family:$font-roboto-regular;
  padding-top: 3.08vh;
  border-top-left-radius: 14px;
  border-top-right-radius: 14px;
  background-color: white;
}
.profileCard .profileIcon {
  width: 11.07vw;
  height: 5.17vh;
  margin-top: 2.4vh;
  margin-bottom: 2.52vh;
}
.manage-profile-arrow-col {
  margin-top: 4.3vh;
  padding-left: 4.27vw !important;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding-left: 6.57vw !important;
  }
}
.manage-header {
  .header-md:after {
    height: 0 !important;
  }
}
.manage_options {
  margin-right: 16px;
  margin-left: 16px;
}
.admincount {
  font-size: 13px;
  color: #ff6418;
  padding-left: 5px;
  font-family: $font-gtamerica-regular;
}
.manage-list {
  margin-top: 62px;
  box-shadow: 0px 3px 6px #645a531a;
}
.manage_list_item {
  --inner-padding-end: 0;
  --padding-end: 4.27vw;
  --min-height: 47px;
  color: #000000;
  font-size: 16px;
  --border-color: #c0c0c0;
  font-family: $font-gtamerica-bold;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    --inner-padding-end: 0.5vw;
    --padding-end: 1.27vw;
  }
}
.manage_list_item item-native {
  --height: 56px !important;
  --padding-top: 14px !important;
}
.profileHeading {
  font-size: 16px;
  color: #000000;
  font-family: $font-gtamerica-bold;
}
.manage-card-content {
  --padding-left: 0px;
  --padding-right: 0px;
  --width: 100%;
  --height: auto;
  --background: linear-gradient(
    97deg,
    #f85f14 32%,
    #f85f14 32%,
    #f85f14 32%,
    #e9e5e1 33%,
    #e9e5e1 10%
  );
  .load-grid {
    --padding: 20px 10px;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    background: linear-gradient(
      97deg,
      #f85f14 42%,
      #f85f14 42%,
      #f85f14 42%,
      #e9e5e1 43%,
      #e9e5e1 10%
    );
  }
}

.page-manage {
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) { 
    height: auto;
    background: none;
    padding: 0;
  }
}