@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";
.carrier_select{
    background: #FFFFFF;
    border: 1px solid #F4F4F4;
    opacity: 1;
    color: #000000;
    width: 41.60vw;
    height:42px;
    font-size:13px;
    float: right;
    border-radius:1px;
    font-family: $font-gtamerica-regular;
    .down {
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
      }
      i {
        border: 1px solid #FF6418;
        border-width: 0 1px 1px 0;
        display: inline-block;
        padding: 3px;
        margin-top: -35px;
        float: right;
        margin-right:2.66vw;
      }
      .search_sort_button{
        text-transform: none !important;
        font-size: 13px;
        height:38px;
        margin-top:11px;
        margin-left:10px;
        width: 90%;
        .page-title-p{
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          padding-left: 0px;
          max-width: 87%;
        }
      }     
}
.search_options_carrier{
  box-shadow: 0 3px 6px #3a4c8230;
  background-color: #ffffff;
  margin: auto;
  width: 92vw;
  height: auto;
  border: 0.5px solid #0000003d;
}
.search_options_carrier .carrier-dropdown{
  padding:11px 0 0.92vh 5.60vw !important;
  font-size:16px;
  font-family: $font-gtamerica-bold;
  border-bottom: 0.25px solid #0000003F;
}
.count-div{
  position: absolute;
  left: 125px;
  top: 8px;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  font-family: $font-gtamerica-regular;
  background:#F85F14;
  color:#FFFFFF;
  text-align: center;
  padding-top: 2px;
  font-size:16px;
  @media only screen 
  and (min-device-width: 411px){
    left:60.73vw;
  }
}
.search_options_carrier .circle-div2{
  top:4px;
  left :15px;
}
.modal-div{
  display: flex;
  height: 100%;
  width: 100%;
}