@import '../../../theme/variables.scss';
@import '../../../theme/responsive-styles.scss';

.menu_page {
  #header-title-sm {
    padding-left: 85px;
  }
  .start-trip-header {
    font-size: 16px;
    padding-left: 4px;
  }
  .page-header {
    height: 9.98vh;
  }
  .menu_page_div{
    padding-bottom:20px;
  }
  padding: 0;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding: 0px 6.38vw 0px 6.38vw;
    .page-header-searchResults {
      display: none;
    }
  }
}
.desktop-page-faq {
  #header-title-more {
    font-size: 4.8vw;
    color: #f85f14;
    text-align: left;
    font-family: 'GT America Bold';
    padding: 34.5px 0 12px 0px;
    margin: auto;
    width: 1%;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding: 0px 9.38vw 190px 9.38vw;
    .header-title {
      display: none;
    }
    .page-header-searchResults {
      height: 50px;
    }
    .desktop-header-desc ion-grid {
      margin-top: -10px;
    }
  }
}
.desktop-page-menu {
  .question-answer {
    display: none;
  }
  .list_col {
    padding-left: 0px;
  }
  .header-desc-col {
    display: none;
  }
  #header-title-more {
    font-size: 4.8vw;
    color: #f85f14;
    text-align: left;
    font-family: 'GT America Bold';
    padding: 34.5px 0 12px 0px;
    margin: auto;
    width: 1%;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding: 0px 9.38vw 190px 9.38vw;
    .header-title {
      display: none;
    }
    .page-header-searchResults {
      height: 50px;
    }
    .desktop-header-desc ion-grid {
      margin-top: -10px;
    }
  }
}
.rightArrow {
  width: 6px !important;
  // height:8px;
}
.menu_page_content {
  --padding-top: 2.59vh !important;
  --padding-start: 2.67vw !important;
  --padding-end: 2.67vw !important;
  --background: #e9e5e1;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    --padding-top: 40px !important;
    --padding-bottom: 20vh !important;
  }
}
.menu_list_item {
  --inner-padding-end: 0;
  --padding-end: 10px;
  --padding-start: 10px;
  --min-height: 42.5px;
  color: #000000;
  font-size: 15px;
  font-family: $font-gtamerica-regular;
  --padding-top: 0.8%;
  --padding-bottom: 0.8%;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    --inner-padding-end: 0.5vw;
    --padding-end: 1.27vw;
    --padding-top: 0.8%;
    --padding-bottom: 0.8%;
  }
}
.menu_list_item2 {
  --inner-padding-end: 0;
  --padding-end: 4.27vw;
  --min-height: 5.05vh;
  color: #000000;
  font-size: 15px;
  --border-color: #0000003f;
  font-family: $font-gtamerica-regular;

  --padding-top: 0.8%;
  --padding-bottom: 0.8%;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    --inner-padding-end: 0.5vw;
    --padding-end: 1.27vw;
    --padding-top: 0.4%;
    --padding-bottom: 0.4%;
  }
}

.menu_list_heading {
  font-size: 16px;
  color: #645a53;
  font-family: $font-gtamerica-bold;
  padding-left: 4.36vw;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    padding-left: 1.36vw;
  }
}
.menu_list_card {
  margin: 0.74vh 0 1.85vh 0;
  box-shadow: 0 3px 6px #3a4c8212;

  @media (min-width: map-get($grid-breakpoints, lg)) {
    ion-list {
      padding-left: 1.27vw;
      padding-right: 0;
    }
  }
}
.menu_list_card2 {
  margin: 10px 5px 18px 5px;
  border-radius: 0;
  box-shadow: none;
}

.menu_list_card2:last-child {
  margin-bottom: 0px;
}

.list_row {
  border-bottom: 1px solid #3a4c8212;
}
.menuList_row {
  padding-top: 11px;
}
.list_row_heading {
  font-size: 16px;
  color: #000000;
  font-family: $font-gtamerica-medium;
  padding-left: 0px;
}
.list_row_description{
  padding-left: 0px;
}
.list_col {
  font-size: 13px !important;
  color: #645a53;
  font-family: $font-gtamerica-regular;
  padding-left: 19px;
}
.menu_page_card {
  --padding-start: 11.5px;
  --padding-end: 8.5px;
}
.menu_list_page_content {
  --background: #e9e5e1;
  --padding-top: 26px !important;
  --padding-start: 14px;
  --padding-end: 14px;
  @media (min-width: map-get($grid-breakpoints, lg)) {
    --padding-start: 1px;
    --padding-top: 0px !important;
  }
  ion-card-content {
    padding-left: 10px;
    padding-right: 10px;
  }
  p{
    padding: 0px;
  }
  .accordion_header {
    padding: 13px 16px 16px 11px;
    p {
      padding: 0px;
      margin: 0px;
      font-size: 16px;
      color: #000000;
    }
    span {
      color: #000000;
    }
    .title-col {
      padding: 2px 0 5px 0;
    }
  }
  .plus-icon {
    width: 8px;
    height: 8px;
    float: right;
    margin-top: 5px;
    // margin-right: -3px;
  }
  .question-answer {
    padding: 0px 3px 0 0;
    float: left;
    color: #ff6418;
    font-size: 16px;
    font-family: $font-gtamerica-medium;
    margin-top: -1px;
  }
  .p-answer {
    margin-top: -3.7px;
  }
  .list_column {
    padding-left: 0px;
  }
}
.youtubeurl {
  color: #ff6418;
  font-size: 13px;
  font-family: $font-gtamerica-bold;
}

  @media (min-width: map-get($grid-breakpoints, lg)) {
    .react-player{
      width:100%;
      height:300px;
    }
  }

.description-div{
  padding-bottom: 10px;
}
.htmlParseDiv{
  background: #FFFFFF;
  padding: 0.2vh 2.5vw;
  border: 1px solid #DEDEDE;
  box-shadow: #3A4C8212 0 3px 6px;
}