@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";

.mange-user-desktop {
    position: relative;
    .list-items-header {
        position: absolute;
        top: -16px;
        left: 0;
        width: 100%;
        z-index: 1;
        @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
            position: static;
            margin-bottom: 0;
            margin-top: 15px;
        }
    }
}