@import '../../../../theme/variables.scss';
@import '../../../../theme/responsive-styles.scss';

.feedback-p{
    color: #000000;
    font-size: 13px;
    font-family: $font-gtamerica-regular;
  }
  .notLikely{
    float: left;
  }
  .likely{
    float: right;
  }
  .feedback-row{
    clear: both;
  }
  .feedback-column{
  padding-left:0px;
 }
 .starRating {
    .starRatingIcon{
          width: 6.2vw;
          height: 2.95vh;
          margin-right: 12.17vw;
    }
    .starRatingIcon4{
        width: 6.2vw;
        height: 2.95vh;
        margin-right: 0px;
    }
}
.feedbackImprove{
    font-size: 16px !important;
    font-family: $font-gtamerica-medium;
    color: #000000;
    margin-top: 2.83vh !important;
}
.text-div{
    border: 1px solid #E9E5E1;
    height: 11.20vh;
    margin-top:1.72vh; 
    padding-top: 2px; 
    ion-textarea{
      --placeholder-color: #9C9089;
      --padding-top: 0px !important;
      --padding-bottom: 26px !important;
    }
}
  .feedbackDiv{
    .submit_btn{
      width: 74.4vw;
      border-radius: 0;
      margin-left: 13px;
      margin-top: 20px;
      height: 4.55vh;
      font-size:13px;
      color: #000000;
      font-family: $font-gtamerica-medium;
      --border-radius: 0px;
      --background: white;
      --background-focused: white;
      --background-hover: white;
      border: 1px solid #ff6418;
      --box-shadow: none;
    }
    .starRating-col{
      padding-left: 20px;
    }
    .starRating-row{
      padding-top: 5px;
    }
    .call_btn{
      border-radius: 0;
      --border-radius: 0;
      height: 5.17vh;
      width: 33.91vw;
      color: #000000;
      font-size: 13px;
      font-family: $font-gtamerica-medium;
    }
    .feedback-cancel{
      display: none;
    }
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        .starRating {
            .starRatingIcon{
                  margin-right: 53.1px;
                  width: 1.71vw;
                  height: 2.82vh;
            }
            .starRatingIcon4{
                width: 1.71vw;
                height: 2.82vh;
            }
        }
      .feedback-grid{
        width:33%;
        margin-left:20px;
      }
      .review-grid{
        width:35%;
        margin-left:20px;
      }
      .starRating-grid{
        margin-left:22px;
      }
      .feedback-cancel{
        display: block;
      }
      .submitBtn-row{
        width:36%;
        float:right;
        ion-col{
          padding-right: 0px;
        }
      }
      .review-p{
         float:left;
      }
      .submit_btn{
        border: 1.25px solid #ff6418;
        height: 40px;
        margin-bottom: 15px;
        width: 12vw;
      }
      .call_btn{
        width: 160px;
        height: 40px;
      }
      .starRating-col{
        padding-left:0px;
      }
      .feedbackImprove{
        margin-top: 0px !important;
      }
    }
  }
  .starRating-p{
    font-size: 16px !important;
    text-align: center;
    font-family: $font-gtamerica-medium;
    color: #000000;
  }
  .feedback-modal{
    .modal-div{
      display: flex;
      height: 100%;
      width: 100%;
    }
    .popup-header{
      border-bottom: none;
    }
  }