/* 

This is a  generic font used across the  app for all the text elements
Font : Helvetica Neue
Regular 


This font used exclusively for city names 
Font :Roboto Medium' 
medium 

*/
@font-face {
  font-family:'GT America Bold';
  font-style: regular;
  font-weight: 100;
  src: local('GT America Bold'), local('GT America Bold'), url("/assets/fonts/GT-America-Standard-Bold.ttf") format('truetype');
}
@font-face {
  font-family:'GT America Regular';
  font-style: regular;
  font-weight: 100;
  src: local('GT America Regular'), local('GT America Regular'), url("/assets/fonts/GT-America-Standard-Regular.ttf") format('truetype');
}
@font-face {
  font-family:'GT America Medium';
  font-style: regular;
  font-weight: 100;
  src: local('GT America Medium'), local('GT America Medium'), url("/assets/fonts/GT-America-Standard-Medium.ttf") format('truetype');
}