@import "../../../../theme/variables.scss";
@import "../../../../theme/responsive-styles.scss";

.search_sortby_select {
  background: #ffffff;
  border: 1px solid #f4f4f4;
  opacity: 1;
  color: #000000;
  width: 156px;
  height: 42px;
  border-radius: 5px;
  font-size: 13px;
  border-radius: 1px;
  position: relative;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    display: none;
  }
  .down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
  i {
    border: 1px solid #f85f14;
    border-width: 0 1px 1px 0;
    display: inline-block;
    padding: 3px;
    position: absolute;
    right: 7px;
    top: 15px;
  }
}

.sortArrows {
  width: 42px;
  height: 42px;
  background: #ffffff;
  border: 1px solid #f4f4f4;
  opacity: 1;
  ion-icon {
    height: 9.67px;
    width: 16.02px;
    top: 24%;
    left: 30%;
    position: relative;
  }
}
ion-button {
  background-color: #fcfcfc;
}
.text_BY {
  text-transform: capitalize;
  padding-left: 0px;
  padding-right: 3px;
}
.search_sort_button {
  text-transform: lowercase !important;
  width: 134px;
  margin-top: 11px;
  margin-left: 10px;
  height: 40px;
  font-size: 13px;
  --color: #000000;
  --color-activated: #000000;
  --color-focused: #000000;
  --color-hover: #000000;
  --background: #ffffff;
  --background-activated: #ffffff;
  --background-focused: #ffffff;
  --background-hover: #ffffff;
  --box-shadow: none;
  font-family: $font-gtamerica-regular;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    margin-left: 7px;
    margin-right: 10px;
  }
}

.activated {
  i {
    border: 1px solid white;
    border-width: 0 1px 1px 0;
    display: inline-block;
    padding: 3px;
    margin-top: -4px;
    margin-left: 5px;
  }
}
.sortBy_text {
  font-size: 16px;
  color: #000000;
  font-family: $font-gtamerica-bold;
  text-align: center;
  padding: 0px;
  margin: 0px;
}
.search_options_trailer {
  box-shadow: 0 3px 6px #3a4c8230;
  background-color: #ffffff;
  margin: auto;
  width: 92vw;
  max-height: 275px;
  border: 0.5px solid #0000003d;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    width: 33vw;
  }
}
.search_options {
  box-shadow: 0 3px 6px #3a4c8230;
  background-color: #ffffff;
  margin: auto;
  width: 92vw;
  height: auto;
  border: 0.5px solid #0000003d;

  ion-row:nth-last-child(2) {
    border-bottom: none !important;
  }
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    max-width: 382px;
  }

  @media only screen and (min-width: map-get($grid-breakpoints, md)) {
    max-width: 450px;
  }
}
.dropdown-row {
  border-bottom: 0.5px solid #e9e5e1;
  padding: 6px 0px 4px 0px;
  font-size: 13px;
  color: #645a53;
  font-family: $font-gtamerica-regular;
  margin: 0 15px 0 15px;
}
.dropdown_cancel {
  border: 1px solid #f85f14;
  color: #000000;
  font-size: 13px;
  height: 42px;
  margin-right: 4.8vw;
  margin-left: -6px;
  padding-top: 10px;
  font-family: $font-gtamerica-medium;
  text-align: center;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    margin-right: 21px;
    max-width: 160px !important;
    @media only screen and (min-width: map-get($grid-breakpoints, md)) {
      margin-right: 20px;
      margin: 20px;
    }
  }
  .dropdown-heading {
    padding-top: 7px;
    padding-bottom: 7px;
    margin: 0px;
    border-bottom: 1px solid #0000003f;
  }
  .dropdown-handler {
    border-bottom: none;
    font-size: 14px;
    padding-bottom: 9px;
    margin-top: 10px;
    ion-col {
      text-align: center;
    }
  }

  .dropdown-modal {
    --background: none;
    --width: 100% !important;
    --height: 100% !important;
  }
  .dropdownValue {
    text-transform: capitalize;
  }
  .checked {
    color: #645a53;
    text-transform: capitalize;
    .check {
      display: inline-block;
      transform: rotate(45deg);
      height: 19px;
      width: 9px;
      border-bottom: 2px solid #fd7d4f;
      border-right: 2px solid #fd7d4f;
      float: right;
      margin-right: 12px;
      margin-top: -3px;
    }
  }
  .desktop-sort {
    display: none;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      display: unset;
    }
  }
}
