@import  "../../../theme/variables.scss";

.my-load-desktop-page{
    .segment_control_tabs{
        width: 67.35vw;
        .ion-segment-button{
            width: auto;
        }
    }
    .searchResults_div{
        display: none;
    }
    .my-load-desktop-content{
        --background: #E9E5E1;
    }
    .assign-icon{
        height: 18px;
        width: 22px;
    }
    .driver-name-grid{
        background: linear-gradient(90deg, #F85F14 50%,#7d7169 50%);
         border-radius: 50%;
        height: 30px;
        width: 30px;
        position: absolute;
        margin: 0;
        color: #FFFFFF;
        text-align: center;
        font-size: 12px;
        padding-top: 7px;
    }
}