@import "../../../../theme/variables.scss";
@import "../../../../theme/responsive-styles.scss";

.ion-user-img {
    width: 72px;
    margin-top: 7px;
    margin-left: 20px;
    border-radius: 100%;   
    overflow: hidden; 
}

h4.ion-user-title {
    text-align: left;
    font-family: $font-gtamerica-bold;
    font-size: 16px;
    color: #000000;
    margin: 5px 0;
}

.ion-user-content {
    margin: 4px 4px 10px;
    margin-bottom: 10px;
    border-radius: 0;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
       border-radius: 0;
       box-shadow: none;
       margin: 5px 8px 10px;
       padding: 10px 10px 0;
    }
}

.ion-user-grid {
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        padding-left: 15px;;
    }
}

.ion-user-col {
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        padding: 10px 0;
    }
}

.ion-user-card{
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        background: linear-gradient(97deg, #F3F1EF 9.5%,#FFFFFF 5%, #FFFFFF 10%)
    }
}

.ion-user-info {
    display: flex;
    padding: 5px 0 0;
    label {
        flex: 1 0 30%;
        color: #645A53;
        font-size: 13px;
        font-family: $font-gtamerica-regular;
    }
    span {
        flex: 1 0 70%;
        font-size: 13px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-family: $font-gtamerica-medium;
    }
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        display: block;
        label {
            padding-right: 15px;
        }
    }
}

.ion-image-wrapper {
    // display: inline-block;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        display: inline-block !important;
    }
}

.ion-user-custom-image{
    border-radius: 50%;
    width: 72px;
    height: 72px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin-top: 5px;
    box-shadow: #000000 0px 3px 6px;
    background: linear-gradient( 97deg, #f85f14 50%, #f85f14 50%, #f85f14 50%, #7D736D 33%, #7D736D 10% );
    .ion-user-child-img{
        flex-grow: 1;
        height: 100%;
        display: flex;
        justify-content: center;
        font-size: 45px;
        padding-top: 11px;
        font-family: $font-gtamerica-bold;
        color :#ffffff;
    }
    &.big {
        width: 100px;
        height: 100px;
        margin: 0 auto;
        .ion-user-child-img {
            flex-direction: column;
            padding: 0;
        }
    }
    &.xlarge {
        width: 110px;
        height: 110px;
        text-align: center;
        .ion-user-child-img {
            flex-direction: column;
            padding: 0;
        }
    }
    &.normal {
        width: 80px;
        height: 80px;
        .ion-user-child-img {
            font-size: 31px;
            padding-top: 17px;
        }
    }
}