@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";

.manage-userslist-page{
    --background: hsl(30, 15%, 90%) !important;
    --padding-start: 0;
    --padding-end: 0;
    #header-title-more {
      font-size: 4.8vw;
      color: #f85f14;
      text-align: left;
      font-family: 'GT America Bold';
      padding: 34.5px 0 12px 9.34vw;
      margin: auto;
      width: 1%;
    }
  }

.manage-user-profile-header {
  text-align: center;
  padding: 10px 0 0;
  h4 {
    margin-bottom: 0;
  }
}

.manage-users-wrapper {
  margin-top: 20px;
  padding: 0px 5px;
  margin-bottom: 30px;
}

.manage-users-formWrapper {
  padding: 20px 0px;
  margin: 0 -5px 40px;
  ion-input {
    --padding-top: 0 !important;
    --padding-bottom: 0 !important;
    font-weight: bold;
    font-size: 15px;
    --placeholder-font-weight: normal;
    &.carrierId {
      font-weight: bold;
    }
  }
  .role-desc {
    padding: 5px 15px 0;
    font-size: 13px;
    display: inline-block;
    color: #645A53;
  }
  .role-info {
    position: relative;
    ion-icon {
      position: absolute;
      right: 15px;
      bottom: 5px;
      width: 10px;
    }
  }
  
}

.manage-userslist-form {
  margin-bottom: 15px;
  padding-bottom: 15px;
  position: relative;
  ion-item {
    &.email-field  {
      position: relative;
      z-index: 1;
    }
    &.error {
      --border-color: red;
    }
  }
  ion-label {
    font-size: 13px;
  }
  .roles-list-grid {
    position: absolute;
    top: 65px;
    z-index: 99;
    background: #fff;
    width: 100%;
    padding: 0;
    ion-item {
      --padding-top: 0 !important;
      --min-height: 46px;
    }
    .role-list-label {
      padding-left: 20px !important;
    }
  }
  .role-check-icon
  {
    display: inline-block;
    transform: rotate(40deg);
    height: 13px;
    width: 8px;
    border-bottom: 2px solid #E9E5E1;
    border-right: 2px solid #E9E5E1;
    margin-right: 12px;
    margin-top: -3px;
    position: relative;
    left: 20px;
    &.checked {
      border-bottom: 2px solid #FF6418;
      border-right: 2px solid #FF6418;
    }
  }
  .validate-btn-wrapper {
    --border-color: #fff;
  }
  .validate-btn {
    display: block;
    width: 100%;
    --background: none;
    --background-focused: none;
    --background-hover: none;
    --color: #000;
    --color-activated: #000;
    --color-focused: #000;
    height: 42px;
    --box-shadow: none;
    border: 1px solid #FF6418;
    margin: 0 12.5px;
    text-transform: none;
    font-weight: bold;
    font-size: 13px;
    margin-top: 15px;
    &:focus {
      box-shadow: none;;
    }
  }
}

.manage-userlist-footer {
  ion-button {
    width: 100%;
    height: 56px;
    position: fixed;
    text-transform: none;
    margin-top: -58px;
    font-family: "GT America Bold";
    font-size: 18px;
    margin-left: 0;
  }
  
}

.change-pwd {
  color: #FF6418;
  font-weight: bold;
}

.delink {
  margin-top: 25px;
}

.field-error {
  color: red;
  font-size: 13px;
  padding: 5px 15px 0;
  display: inline-block;
}

.search-input-icon {
  margin-left: 5px;
  width: 13.5px;
  height: 13.5px;
  margin-right: 5px;
}
.static {
  position: static !important;
}
.invalid-btn {
  border-color: #9C9089 !important;
  --background: #9C9089 !important;
  --background-hover: #9C9089 !important;
  --color: #fff !important;
}

.btn_save {
  border-color: #F85F14 !important;
  --background: #F85F14 !important;
  --background-hover: #F85F14 !important;
  --color: #fff !important;
}

.profile-item{
  --padding-start: 0px;
  --min-height: 51px;
}
.profile-label{
  padding-left: 10px;
  font-size: 13px !important;
  font-family: $font-gtamerica-regular !important;
  margin-top:12px;
}
.profile-input{
  --padding-start: 7px !important;
  font-size: 15px !important;
  font-family: $font-gtamerica-medium !important;
  --padding-bottom: 0px !important; 
  margin-top: -10px;
  --padding-top: 12px !important;
}
.changePwd{
  --padding-start: 9px;

}
.manage-user-profile-desktop {
  padding-top: 100px !important;
  .custom-padding  {
    --background: #F8F8F8;
    h1 {
      text-align: center;;
      background-color: #fff;
      font-size: 16px;
      margin: 0 -20px;
      padding: 10px;
    }
    .profile-item {
      width: 48%;
      display: inline-block;
      margin: 10px;
     
    }
    .manage-user-buttons {
      text-align: right;
      ion-grid {
        max-width: 320px;
      }
      
    }
  }
}

.roles-list-grid {
  position: relative;
  top: -30px;
  z-index: 99;
  background: #fff;
  width: 100%;
  padding: 0;
  ion-item {
    --padding-top: 0 !important;
    --min-height: 46px;
  }
  .role-list-label {
    padding-left: 20px !important;
  }
}
.role-check-icon
{
  display: inline-block;
  transform: rotate(40deg);
  height: 13px;
  width: 8px;
  border-bottom: 2px solid #E9E5E1;
  border-right: 2px solid #E9E5E1;
  margin-right: 12px;
  margin-top: -3px;
  position: relative;
  left: 20px;
  &.checked {
    border-bottom: 2px solid #FF6418;
    border-right: 2px solid #FF6418;
  }
}

.my-profile-desktop {
  --background: #F8F8F8 !important;
  .roles-list-grid {
    max-width: 48%;
  }
  .profile-item {
    width: 48%;
    display: inline-block;
    margin: 10px;
    position: relative;
    overflow: inherit;
    &.role-info {
      padding-bottom: 25px;
    }
    .field-error {
      position: absolute;
      top: 100%;
      z-index: 1;
      left: -15px;
    }
    .role-icon {
      position: absolute;
      right: 0;
      top: 50%;
      width: 16px;
    }
    .role-desc {
      position: absolute;
      top: 108%;
    }
    .suggestions {
      position: absolute;
      top: 100%;
      width: 100%;
      height: 250px;
      z-index: 99999;
      background: #fff;
  }
  }
  .manage-user-buttons {
    text-align: right;
    padding-right: 5px;
    margin-bottom: 30px;
    ion-button {
      width: 160px;
      margin-left: 20px !important;
    }
  }
}
.manage-user-card-content{
  background: linear-gradient(
    97deg,
    #f85f14 42%,
    #f85f14 42%,
    #f85f14 42%,
    #e9e5e1 43%,
    #e9e5e1 10%
  );
  .mange-user-desktop{
    top: 50px;
  }
  .addDriver-desktop-header{
    font-size: 16px;
    font-family: $font-gtamerica-medium;
    color: #000000;
    background-color: #FFFFFF;
    height: 43px;
    padding-top: 12px;
    text-align: center;
  }
  .manage-userlist-form{
    padding:0px;
    margin: 15px 15px 0px 15px;
    padding-bottom: 15px;
  }
  ion-item{
    margin-left: 15px;
    --padding-start: 10px;
    margin-right: 25px;
    margin-bottom: 15px;
    position: relative;
    ion-icon {
      position: absolute;
      right: 0;
      top: 25px;

    }
    ion-lable{
      font-size: 13px;
      color: #645A53;
      font-family: $font-gtamerica-regular;
    }
    ion-input{
      font-size: 15px;
      font-family: $font-gtamerica-medium;
      color: #000000;
      --placeholder-color: #645A53;
      --padding-bottom: 0px !important;
      --padding-top: 4px !important;
    }
  }
  .role-desc{
    margin-left: 20px;
    p{
      padding-left:2vw;
      font-size: 14px;
      font-family: $font-gtamerica-regular;
      color: #000000;
    }
  }
  
  .manage-userButtons-row{
    padding-right: 10px;
    padding-top: 10px;
    float: right;
  }
  .manage-user_btn{
    --background: white;
  --background-focused: white;
  --background-hover: white;
    border-radius: 0;
    --border-radius: 0;
    height:40px;
    width: 11.43vw;
    color: #000000;
    border: 1px solid #ff6418;
    font-size: 13px;
    font-family: $font-gtamerica-medium;
    --box-shadow: none;
    margin-bottom:12px;
  }
  .btn_save{
    --background: #9C9089;
    color: #FFFFFF;
    border: 1px solid #9C9089;
  }
  .search-input-icon{
    margin-top: 6px;
  }
}