/*Modified Styles*/
@import "../../../theme/variables.scss";
@import "../../../theme/responsive-styles.scss";

.main-title {
  font-size: 8vw;
  font-weight: bold;
  color: $white;
}
.page-header {
  font-family: $font-gtamerica-regular;
  ion-toolbar {
    background-color: $primary;
    --background: transparent;
  }
}
.login_content_padding {
  --padding-top: 1.97vh !important;
  --padding-start: 6.8vw !important;
  --padding-end: 6.8vw !important;
  --padding-bottom: 1.97vh !important;
  --background: #fcfcfc;
}
ion-router-outlet {
  background-color: #f1f1f1;
}

.signup-btn {
  padding-left: 9px;
}
.signup-buttons {
  position: absolute;
  bottom: 3%;
  text-align: center;
  width: 100%;

  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    width: 74%;
    position: absolute;
    left: 12.8%;
    bottom: 10%;
  }
}
.signIn-col {
  padding: 0px;
}
.signup-btn-col {
  padding-left: 0px !important;
  padding-right: 1.862vw !important;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    text-align: right;
    padding-right: 0px !important;
  }
}

.R2ConfigurationModal-item {
  padding-bottom: 20px;
}

.Landing-screen-content {
  width: 100%;
  background-color: #e9e5e1;
  height: 100%;
  text-align: center;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    width: 100%;
    height: 105vh;
    padding: 148px 143px 147px 142px;
  }
}
.landing-button-img {
  width: 11.62px !important;
  height: 12.26px !important;
}
.globe-image {
  width: 100%;
  height: 27.58vh;
  background-image: url(/assets/images/globe-image@2x.png);
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    width: 100%;
    height: 68.26vh;
    text-align: center;
  }
}
.app-login,
.app-signin {
  --background: #fff !important;
  color: #000000;
  border: 1px solid #f85f14;
  font-family: $font-gtamerica-medium;
  width: 165.8%;
  font-size: 13px;
}
.networkError_div {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #000000;
  z-index: 1;
  opacity: 0.7;
}
.load-btn-Signup {
  width: 100%;
  height: 56px;
  font-family: $font-gtamerica-bold;
  font-size: 18px;
  margin: 0px;
  --border-radius: none;
  text-transform: none;
  --box-shadow: none;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    height: 40px;
    width: 160px;
    font-size: 13px;
  }
}
.getStarted {
  font-size: 26px;
  color: #000000;
  font-family: $font-gtamerica-bold;
  margin: 0;
  padding-left: 0;
  text-align: center;
  padding-top: 10.09vh;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    padding-left: 0;
    padding-top: 9.96vh;
    text-align: center;
  }
}
.StartwithSignin {
  color: #645a53;
  font-size: 16px;
  font-family: $font-gtamerica-regular;
  margin: 0;
  padding: 1.23vh 0 0 0;
  text-align: center;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    text-align: center;
  }
}
.midpart-icon {
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    width: 80.67vw !important;
    height: 37.3vh !important;
    padding-top: 3.13vh;
  }
}
.mid-illustration {
  padding-top: 1.353vh;
  max-width: 99.75vw;
  //height: 44.6vh !important;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
  }
}
.SignInImg {
  width: 3.458vw !important;
  height: 1.5vh !important;
}
.SignUpImg {
  width: 3.14vw !important;
  height: 1.59vh !important;
}
.popup {
  width: auto;
  height: 197px;
  position: absolute;
  background: #ffffff;
  z-index: 1;
  left: 15px;
  top: 0;
  right: 15px;
  bottom: 0;
  margin: auto;
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    width: 400px;
    height: 210px;
  }
}
.no-network {
  color: #000000;
  font-family: $font-gtamerica-bold;
  font-size: 16px;
  text-align: center;
  padding: 0px;
  border-bottom: 1.5px solid #e9e5e1;
  padding-bottom: 10px;
}
.network-content {
  color: #000000;
  font-family: $font-gtamerica-regular;
  font-size: 14px;
  text-align: center;
  padding: 12px 43px;
}
.retry_button {
  border: 1px solid #f85f14;
  font-size: 13px;
  font-family: $font-gtamerica-medium;
  --background: white;
  border-radius: 0px;
  color: black;
  left: 0px;
  top: 111px;
  bottom: 0px;
  right: 0px;
  margin: auto;
  position: absolute;
  width: 132px;
  height: 42px;
}
.app-signin {
  margin-left: 10px;
}
.root-page {
  .login-contianer {
    background: #f1f1f1;
    padding: 3.09vh 0 1.23vh;
    .login_form_feild {
      background-color: #fcfcfc;
      ion-item {
        --background: #fcfcfc;
      }
      ion-img:last-child {
        cursor: pointer;
      }
    }

    input {
      color: #000000;
      padding-bottom: 1.23vh;
      font-size: 15px;
    }
    ion-label {
      color: #999999;
      font-size: 15px;
      margin-top: 1.48vh;
    }
    ion-item:first-child {
      margin-bottom: 1.85vh;
    }
    span {
      font-size: 13px;
      float: right;
      color: #333333;
      // margin-right: 1.6vw;
      margin-top: 0.86vh;
    }
  }
  .login_remember_link {
    ion-col {
      padding: 0;
    }
    padding-bottom: 6.17vh;
    .login_remember_text {
      float: left;
      padding-left: 1.33vw;
    }
  }
  .-container {
    margin: 3vh 0vw;
  }
  .login-btn {
    --border-radius: 35px;
    height: 12vw;
    font-size: 18px;
    border-radius: 35px;
    background: linear-gradient(180deg, $loginBtnTop, $loginBtnTop);
    --background-activated: linear-gradient(180deg, #fe8347, #fe8347);
    .button-solid {
      background: $primary;
      --background-focused: $primary;
      --background-hover: $primary;
      border-radius: 35px;
      background-color: $primary !important;
    }
  }
  .disabled-login {
    --background: #c0c0c0 !important;
    --border-radius: 35px;
    height: 12vw;
    font-size: 18px;
    .button-solid {
      background: #c0c0c0 !important;
      --background-focused: #c0c0c0 !important;
      --background-hover: #c0c0c0 !important;
      border-radius: 35px;
      background-color: #c0c0c0 !important;
    }
  }
  .login_alternate {
    // padding: 0 1vh;
    clear: both;
    margin-top: 2.6vh;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      margin: auto;
      width: map-get($grid-breakpoints, xs);
      margin-top: 2.6vh;
    }
    .login_withmobile_btn {
      --background: white;
      --background-focused: white;
      --background-hover: white;
      --background-activated: white;
      color: $primary;
      border: 1px solid;
      border-radius: 35px;
      font-size: 14px;
      margin-bottom: 3.7vh;
      --color-activated: $primary;
      --color-focused: $primary;
    }
    .sign_up {
      text-align: center;
      .signup_link {
        font-size: 15px;
      }
    }
  }
  .list-ios {
    margin-bottom: 1.23vh;
  }
  .container-title {
    font-size: 2vw;
    color: $primary;
  }
  .login_input_icon {
    padding-bottom: 0;
    margin-top: 4.32vh;
    margin-right: -1.07vw;
  }
  .img_div {
    float: left;
  }
  .login_devider_line {
    background-color: $silver;
    width: 40%;
    float: left;
  }
  .login_devider_text {
    float: left;
    font-size: 16px;
    color: $nobel;
    padding: 0 3.2vw;
  }

  .login_logo_container,
  .login_logo_container_change {
    background: linear-gradient(180deg, #fca364, #fd7d4f);
    height: 22.6vh;
    -webkit-transition: height 0.5s ease-in-out;
    -moz-transition: height 0.5s ease-in-out;
    -o-transition: height 0.5s ease-in-out;
    transition: height 0.5s ease-in-out;
    #header_logo {
      margin-top: 9.26vh;
      -webkit-transition: margin-top 0.5s ease-in-out;
      -moz-transition: margin-top 0.5s ease-in-out;
      -o-transition: margin-top 0.5s ease-in-out;
      transition: margin-top 0.5s ease-in-out;
    }
  }

  .login_logo_container_change {
    height: 12.35vh;
    #header_logo {
      margin-top: 3.7vh;
    }
  }
  ion-toggle {
    --background-checked: #f6d679;
    zoom: 0.8;
    // float: right;
  }
  .ion-toggle-checked {
    --background-checked: #f6d679;
    zoom: 0.8;
    border-radius: 35px;
    background: #f6d679;
    --background-activated: #f6d679;
  }

  #errorMsg {
    margin: auto;
    height: 3.7vh;
    width: 100%;
    text-align: center;
    font-size: 11px;
    color: #ff0000;
    font-family: $font-gtamerica-regular;
  }
  .dot {
    height: 1.23vh;
    width: 2.7vw;
    background-color: red;
    border-radius: 50%;
    display: inline-block;
  }
  #errorDot {
    margin: auto;
    text-align: center;
    font-size: 11px;
    color: white;
  }

  #header_logo {
    align-content: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      width: 10%;
    }
    @media only screen and (min-width: map-get($grid-breakpoints,md)) {
      width: 25%;
    }
  }

  .login_devider {
    padding: 3.2vh 0 1.6vh 3.5vw;
  }

  .login_error {
    --border-color: #ff0000;
  }
  /* Desktops and laptops ----------- */

  @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    /* Styles */
    .login-contianer {
      margin: auto;
      width: map-get($grid-breakpoints, xs);
      span {
        margin-top: 0.5vh;
      }
    }
    .login_input_icon {
      margin-right: -0.07vw;
    }
    .disabled-login {
      height: 45px;
      margin: 0;
    }
    .login-btn {
      height: 45px;
      margin: 0;
    }
    .login_devider {
      padding: 3.2vh 0 1.6vh 1vw;
    }
    .login_devider_text {
      padding: 0px 0.6vw;
    }
    .login_remember_link {
      .login_remember_text {
        padding-left: 0.33vw;
      }
    }

    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
      /* Styles */

      ion-content {
        margin: auto;
        padding-right: calc((100% - #{map-get($grid-breakpoints, lg)}) / 2);
        padding-left: calc((100% - #{map-get($grid-breakpoints, lg)}) / 2);
      }
    }
  }
}
