.assignmodal {
  .modal-wrapper {
    width: 92%;
    max-height: 40%;
    .ion-page {
      background-color: #ffffff;
    }
  }
  header {
    text-align: center;
    padding: 15px;
  }
  section {
    border: 1px solid #E9E5E1;
    max-height: 200px;
    overflow-y: scroll;
    overflow-x: hidden;
    .ion-card-updated {
      margin-bottom: 0px !important;
    }
  }
  footer {
    text-align: center;
    padding-bottom: 20px;
    padding-top: 20px;
    .load-btn{
      border: 1px solid #f85f14;
    }
  }
  .btn {
    width: 132px;
  }
}
