@import "../../../../theme/variables.scss";
@import "../../../../theme/responsive-styles.scss";

.list-items-header {
    margin-top: 15px;
    margin-bottom: 15px;
}
.list-items-title {
    line-height: 40px;
    display: block;
    height: 40px;
    margin-left: 5px;
    font-size: 13px;
}
.list-items-filter
{
    display: -webkit-inline-box;
    height: 21px;
    float: right;
    margin-right: 5px;
}

.filter-dropdown {
    text-transform: capitalize !important;
    width: 150px;
    margin-top: 0px;
    height: 40px;
    font-size: 13px;
    font-family: "GT America Regular";
    display: inline-block;
    background-color: #fff;
    margin: 0;
    line-height: 40px;
    padding-left: 10px;
    position: relative;
    i {
        border: 1px solid #f85f14;
        border-width: 0 1px 1px 0;
        display: inline-block;
        padding: 3px;
        position: absolute;
        right: 7px;
        top: 16px;
        &.down {
            transform: rotate(45deg);
            -webkit-transform: rotate(45deg);
          }
    }
}

.filter-sort {
    padding: 10px 10px;
    background: #fff;
    border-left: 1px solid #efefef;
    font-family: $font-gtamerica-regular;
    position: relative;
    height: 40px;
    box-sizing: border-box;
    display: inherit;
}

.checked {
    color: #645a53;
    .check {
      display: inline-block;
      transform: rotate(45deg);
      height: 19px;
      width: 9px;
      border-bottom: 2px solid #fd7d4f;
      border-right: 2px solid #fd7d4f;
      float: right;
      margin-right: 12px;
      margin-top: -3px;
    }
  }
  .manage_add_icon{
      width:10px;
      height: 10px;
      margin-right: 8px;
  }
  .add-driver-text{
      font-size: 14px;
      font-family: $font-gtamerica-medium;
      color: #FF6418;
      margin-right: 26px;
  }