@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";

.carrier-assign-page{
    --background: #E9E5E1;
    .page-header{
        height: 72px;
    }
}
.driverModal-content{
    --background: #E9E5E1;
}
.carrierAssignedContent{
    --padding-top: 16px !important;
    --padding-start: 4vw !important;
    --padding-end: 4vw !important;
    --background: #E9E5E1;
    .CarrierLoads{
        padding: 46.5px 15.5px 0px 15.5px;
    }
    .end-of-list{
        height: 30px;
        margin: auto;
    }
    .card-content .load-grid{
         padding: 15px 10px 20px 10px;
    }
}
.pop_row{
    height:45px;
}
.cancel_text{
    width:22px;
    height:14px;
    font-size: 20px;
}
.driver-assign-confrim{
    --start: 4vw !important;
    --end: 4vw !important;
    max-height: 90%;
    font-family: $font-gtamerica-regular !important;
    font-size: 14px !important;
    --background: #3D3D3E !important;
    // --height: 40px !important;
    --box-shadow: 0px 3px 6px #3A4C8212 !important;
    .toast-content{
        padding-top: 10px !important;
    }
    
}
.sub-header-grid{
    padding: 0;
    height: 42px;
    margin-bottom: 16px;
    .dropdown-col{
        padding: 0;
    }
    .count-col{
        font-size: 13px;
        color: #645A53;
        padding: 0;
        margin-top: 13px;
    }
}
.Driver-list-modal {

.modal-wrapper.sc-ion-modal-md{
    width: 1082px;
    height: 650px;
    margin-top:19px;
}
}
.assign-btn{
    margin-top: 29px;
    margin-bottom: 25px;
    margin-right: 21.27px;
}
.addUser_img{
    width: 18px;
    height: 14px;
    margin-right: 7px;
    margin-top: 2px;
}
@media only screen and (min-width: map-get($grid-breakpoints, lg)) {
    .load-btn{
        width: 155.22px;
        height:40px;
        white-space: break-spaces;
    }
}
