@import "../../../theme/variables.scss";
@import "../../../theme/responsive-styles.scss";

.registration-page {
  --background: #e9e5e1;
  --padding-top: 30px;
  --padding-start: 15px;
  --padding-end: 15px;
  .logo {
    height: 70px;
    width: 240px;
    margin-bottom: 40px;
  }
  .page-close {
    color: #000;
    height: 40px;
    width: 40px;
    float: right;
    margin: -11px -11px 0 0;
  }
  .titel-grid {
    padding: 0px;
    font-size: 26px;
    font-family: $font-gtamerica-bold;
  }
  .registration-form {
    padding-bottom: 20px;
    margin-top: 10px;
    padding-top: 0px;
    margin-bottom: 0px;
    ion-item {
      --padding-start: 8px;
      --border-color: #e9e5e1;
      --min-height: 46px;
      --background-focused: transparent;
      ion-input {
        --padding-top: 0px;
        --padding-bottom: 0px;
        font-size: 15px;
      }
      ion-label {
        margin-top: 20px;
      }
    }
    ion-radio-group {
      margin-bottom: 10px;
    }
    .radiobutton {
      margin: 0px;
      height: 23px;
      width: 23px;
    }
    .radiobutton-label {
      font-size: 13px;
      padding: 8px 10px 5px 5px;
    }

    .select-role-grid {
      padding: 17px 0 0 8px;
      border-bottom: 1px solid #e9e5e1;

      .select-role-label {
        font-size: 13px;
      }
      .role-name {
        padding: 5px 0px 0 0;
        font-size: 15px;
      }
      .dropDown-icon {
        text-align: right;
        padding-right: 15px;
        ion-icon {
          height: 10px;
          width: 10px;
        }
      }

      .role-suggetion-grid {
        box-shadow: 0px 3px 6px #3a4c8230;
        width: 100%;
        border: 0.5px solid #00000021;
        background: white;
        z-index: 9;
        position: absolute;
        margin-left: -8px;
        .opt-row {
          font-size: 15px;
          padding: 12px 14px 21px 16px;
          .check {
            display: inline-block;
            transform: rotate(45deg);
            height: 14px;
            width: 8px;
            border-bottom: 2px solid #e9e5e1;
            border-right: 2px solid #e9e5e1;
            float: right;
            margin-right: 12px;
            margin-top: 3px;
          }
          .role-desc {
            font-size: 12px;
            color: #666666;
          }
          ion-col {
            padding: 0px;
          }
        }
        .selected {
          .check {
            border-color: #ff6418;
          }
        }
        .opt-row:first-child {
          border-bottom: 0.5px solid #e9e5e1;
        }
      }
    }
  }
}
.bottom-grid {
  background: #e9e5e1;
  .register-btn {
    margin: 0 0 36px 0;
    height: 56px;
    --background: #9c9089;
    text-transform: none;
    font-family: $font-gtamerica-bold;
    font-size: 18px;
  }
  .active-btn {
    --background: #ff6418;
    margin: 0 0 36px 0;
    height: 56px;
  }
}
@media only screen and (min-width: map-get($grid-breakpoints, lg)) {
  .registration-page {
    --padding-start: 105px;
    --padding-end: 105px;
    --padding-bottom: 30px;
    .logo {
      width: 97%;
      margin: 40px auto 25px auto;
    }
    .titel-grid {
      font-size: 22px;
      font-family: $font-gtamerica-medium;
      margin-bottom: 18px;
    }
    .select-role-label {
      font-size: 12px;
      font-family: $font-gtamerica-regular;
      color: #000000;
    }
    .role-name,
    ion-input {
      font-size: 14px;
      font-family: $font-gtamerica-medium;
      color: #000000;
      --placeholder-color: #645a53;
    }
    .role-suggetion-grid {
      width: 88% !important;
    }
    .opt-row {
      padding: 5px 14px 13px 0px !important;
      font-size: 14px !important;
    }
    .opt-col-i {
      padding-top: 6px !important;
      margin-left: -2px !important;
    }
    .bottom-grid {
      background: #ffffff;
      margin-top: 30px;
      .bottom-grid-row {
        // width:27.72vw;
        float: right;
        margin-bottom: -8px;
        padding-right: 25px;
      }
      .submit_btn {
        border-radius: 0;
        margin-left: 13px;
        margin-top: 0px;
        color: #000000;
        --border-radius: 0px;
        --background: white;
        --background-focused: white;
        --background-hover: white;
        border: 1.25px solid #ff6418;
        --box-shadow: none;
      }
      .register-btn {
        margin: 3px 0 0 0;
        --background: #9c9089;
        color: #ffffff;
      }
      .cancel-button {
        --background: #fff;
        --border-radius: 0;
        --box-shadow: none;
        border: 1.25px solid #ff6418;
        color: #000;
        --background-activated: #fff;
        --background-focused: #fff;
        --background-hover: #fff;
        letter-spacing: 0;
      }
      ion-button {
        width: 12vw;
        height: 4.5vh;
        text-transform: none;
        font-family: $font-gtamerica-medium;
        font-size: 13px;
      }
    }
  }
  .errorGrid {
    display: block !important;
    height: 15px;
  }
}
.registration-form-col {
  padding-left: 36px;
  padding-right: 35px;
}
.registration-col {
  padding-top: 8px;
  padding-right: 30px;
}
.errorGrid {
  font-size: 13px;
  color: red;
  padding: 0 5px;
  display: none;
}
.showErrorGrid {
  display: block;
}
