@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";
.driverContent{
    --padding-top: 1.10vh;
    --background: #E9E5E1;
}
.driverCard{
    
    --padding-end: 4.53vw;
    --border-color: #DEDEDE; 
    margin-top: 1.1vh;
    --background: #FCFCFC;
   
    .DriverName{
        font-size: 16px;
        color: #FF6418;
        font-family: $font-gtamerica-regular;
    }
    .DriverPara{
        margin-top: 2px;
        padding-left: 0;
        font-size: 10px;
        color: #000000;
        margin-bottom: 0;
        font-family: $font-gtamerica-regular;
    }
    
    
    .driverRow{
        margin-bottom: 0.5vh;
        width: 91.7vw;
        height: 71px;
  
    }
    
    .DriverImageCol{
        padding-left:0px;
    }
}
.driverList-page{
    .page-header{
        height: 9.8vh;
    }
} 
.add-driver-icon{
    width: 5.48vw;
    height: 2.8vh;
}
.add-driver-circle{
    width: 12vw;
    height: 5.54vh;
}



.driverCol{
    padding: 16px 22px 16px 14px !important;
}


.driver-card{
    border-radius: 0;
    margin: 10px 14px 15px 14px;
    opacity: 1;
}
.DriverImage{
    width: 72px !important;
   
    border-radius: 50%;
    height: 72px !important;
    
}
.image-letter{
    font-size: 36px;
    color: var(--ion-color-primary);
}
.image-letter-background{
    background-color:red;
}

//newdesign 
.driver-card-content{
    padding-left: 0px;
    padding-right: 0px;
      width: 100%;
      height: auto;
      background: linear-gradient(95deg, #F3F1EF 31%, #F3F1EF 32%, #F3F1EF 32%, #FFFFFF 21%, #FFFFFF 10%);
      .load-grid{
      padding: 20px 10px;
    }
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        background: linear-gradient(96deg, #F3F1EF 2%, #F3F1EF 9%, #F3F1EF 11%, #FFFFFF -18%, #FFFFFF -10%);
    }
}
.driver-name{
    font-size: 16px !important;
    color:#000000;
    font-family: $font-gtamerica-bold;
    padding-left: 16px;
    padding-top: 13px;
}
.tractorno{
    color: #645A53;
    padding-top: 4px;
    font-size: 13px;
    font-family: $font-gtamerica-regular;
    padding-left: 16px;
}
.tractorno2{
    color: #645A53;
    padding-top: 0px;
    font-size: 13px;
    font-family: $font-gtamerica-regular;
    padding-left: 16px;
}
.tractor-number{
    padding-top: 0;
    font-size: 15px;
    color:#000000;
    font-family:$font-gtamerica-medium;
}
.card-city-col{
    padding: 0;
}
.select-driver-header{
font-size: 14px;
color: #000000;
font-family:$font-gtamerica-bold;
}
.popup-add-driver{
font-size: 14px;
color:var(--ion-color-primary);
font-family:$font-gtamerica-medium;
margin-right: 10px;
    letter-spacing: 1px;
}
.add-right{
text-align: right;
}
.add_img{
    width: 15px;
    float: left;
    height: 15px;
    margin-left: 227px;
    margin-top: 2px;
}
.select-driver-main-Col{
background: #FFFFFF 0% 0% no-repeat padding-box;
}
.Background-popup{
    background-color: #E9E5E1;
}
.cancel_col{
    padding-left: 14px;
    padding-top: 5px;
    background:#F3F1EF;
}
.Assign-btn-view{
    display: none;
    padding-left: 81px;
    padding-right:14px;
    @media only screen  and (min-width : map-get($grid-breakpoints, lg) ) {
       display: block;
       padding-left: 35.27px;
      }
}

.driver-img-div{
    border-radius: 50%;
    width: 72px;
    height: 72px;
    border: 1px solid lightgrey;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    .child-img{
flex-grow: 1;
height: 100%;
display: flex;
    justify-content: center;
    font-size: 33px;
    padding-top: 11px;
    font-family: $font-gtamerica-bold;
    color :#ffffff;
    

    }
span:first-child{
    background-color: #f85f14;
      
    }
span:nth-child(2){
    background-color:#7D736D;
    }
}