
@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";

.logout_modalPopUp{
    width: 92%;
    margin: auto 15px;
    background: white;
    .modalPopUp_text{
        font-size: 13px;
        text-align: center;
        line-height: 18px;
        color:#645A53;
        opacity: 1;
        padding: 0px;
        margin-bottom: 19px;
    }
    .logoutModalButtons{
        --background: white;
        --background-activated: #FFFFFF;
        --background-focused: #FFFFFF;
        --background-hover: #FFFFFF;
        width: 132px;
        height: 42px;
        margin: 0px 10px;
        --color: #000000;
        --color-activated: #000000;
        --color-focused: #000000;
        --color-hover: #000000;
        border: 1.25px solid #FF6418;
        font-size: 13px;
        font-family: $font-gtamerica-medium;
        --box-shadow: none;
        text-transform: capitalize;
        text-align: center;
        @media only screen 
        and  
        (device-aspect-ratio: 40/71)
         { 
         width: 110px;
          }
       
       
    }
    .popup-header{
        padding: 16px 0 0 0;
        height: 46px;
        text-align: center;
        border-bottom: 1.5px solid #E9E5E1;
        font-size: 16px;
        font-family: $font-gtamerica-bold;
    }
    .popup-body{
        padding: 0 16px;
        text-align: center;
        @media only screen 
        and  
        (device-aspect-ratio: 40/71)
         { 
          text-align: inherit;
          }
        .button-div{
            width: 305px;
            margin: auto auto 20px auto;
        }
    }
    @media only screen and (min-width: map-get($grid-breakpoints, lg)) {
        width: 382px;
        margin: auto;
    }
   
}

ion-modal{
    --background: #000000 !important;
}